.image-masonry {
	column-count: 1;
	column-gap: 1.5rem;
	padding: 0;
	width: 100%;
	max-width: 1300px;
	margin: 0 auto 1.5rem auto;
}

.masonry-item {
	display: block;
	width: 100%;
	box-sizing: border-box;
	margin-top: 1.5rem;
	&:first-child {
		margin-top: 0;
	}
	> img {
		width: 100%;
		display: block;
		margin: auto;
		text-align: center;
		max-height: 470px;
		max-width: 370px;
	}
}

@media only screen and (min-width: 576px) {
	.image-masonry {
		column-count: 2;
	}
}

@media only screen and  (min-width: 768px) {
	.image-masonry {
		column-count: 3;
	}
}
