.activities {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: $body-max-width;
  position: relative;

  @media (max-width: 768px) {
    margin-bottom: 0px !important;
  }

  &-yelp-logo {
    position: relative;
    left: 0;
    bottom: 0;
    display: inline-block;
    max-width: 80px;
  }
  &-container {
    width: 100%;
    padding-left: 2rem;
    padding-right: 2rem;
    p {
      @extend .vacation-text;
      text-align: center;
      line-height: 1.5;
      margin-top: 1rem;
      margin-bottom: 2rem;
    }
  }
  &-title {
    font-family: "Baskerville BT WGL4 W05 Roman", sans-serif;
    font-size: 52px;
    text-align: center;
    &-container {
      width: 50%;
      margin: auto;
    }
    &-underline {
      @extend .header-underline;
      &:after {
        background-color: $activities-accent;
        width: 37px;
        height: 7px;
        left: 50%;
        transform-origin: center;
        transform: translateX(-50%);
      }
    }
  }
  &-menu {
    &-container {
      @extend .resort-menu-container;
      background-color: $white;
    }
    &-wrapper {
      @media (max-width: 1070px) {
        display: none !important;
      }
    }
    padding-left: 5%;
    padding-right: 5%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    border-top: 1px solid $gray-element;
    border-bottom: 1px solid $gray-element;
    &-option {
      cursor: pointer;
      &:first-child {
        border-left: 1px solid $gray-element;
      }
      border-right: 1px solid $gray-element;
      font-size: 14px;
      text-transform: uppercase;
      color: $copy-color;
      flex-grow: 1;
      text-align: center;
      padding-top: 0.5rem;
      padding-bottom: 0.5rem;
      .form-check {
        margin: 0;
        &-label {
          margin: 0;
          padding-left: 0.25rem;
          cursor: pointer;
        }
        &-input {
          cursor: pointer;
          &:only-child {
            position: absolute;
          }
          &:focus {
            outline: none;
          }
        }
      }
    }
  }
  &-interface {
    height: 500px;
    border: solid 1px #d5d5d5;
    border-left: none;
    border-right: none;
    border-top: none;
    @media (max-width: 768px) {
      height: auto;

      border-bottom: none;
    }
  }

  &-list {
    &-container {
      padding: 0;
      height: 458px;
      overflow-y: scroll;
      @media (max-width: 768px) {
        overflow-y: auto;
        height: auto;
      }
    }
    &-item {
      padding: 0.5rem 0 0.5rem 0;
      text-align: left;

      .site-active {
        background-color: #79726c;
      }

      &:last-child {
        .break {
          display: none;
        }
      }
      &:hover {
        .activities-list-item-header {
          text-decoration: underline;
        }

        .activities-list-item-header-onsite {
          text-decoration: none;
        }
      }
      a {
        text-decoration: none;
      }
     
      &-picture {
        height: 100px;
        overflow: hidden;
        img {
          width: 100%;
          height: auto;
          position: relative;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
        }

        @media (max-width: 768px) {
          display: none !important;
          overflow: auto;
        }
      }
      &-stars {
        img {
          margin-right: 0.5rem;
        }
        span {
          font-size: 11px;
        }
      }
      .container {
        padding-left: 0rem;
      }
      &-content {
        padding-left: 1.5rem;
      }
      &-header {
        font-size: 16px;
        color: $secondary-color;
      }
      &-tag {
        font-size: 14px;
        color: $secondary-color;
      }
      &-extra {
        margin-top: 0.5rem;
      }
      &-categories {
      }
    }
  }
  &-filter {
    &-menu {
      width: 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      padding: 0;
    }
    &-button {
      &:first-child {
        border-right: 1px solid $resort-menu-divider-color;
      }
      text-align: center;
      flex: 1 1 auto;
      background-color: $copy-color;
      font-size: 16px;
      color: $white;
      text-transform: uppercase;
      padding: 0.5rem 2rem;
      white-space: nowrap;
      &:hover {
        text-decoration: none;
        @extend .resort-selection-active;
      }
    }
  }

  &-map-container {
    padding: 0;
    margin: 0px auto;

    @media (max-width: 768px) {
      display: none !important;
    }

    img {
      width: 100%;
    }

    &-onsite {
      display: none;
    }

    &-offsite {
      border-top: solid 1px #d5d5d5;
    }
  }
}

#map {
  height: 498px;
}

.onsite-img {
  max-height: 100%;
}

.activities-list-container:after {
  content: "";
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 1)
  );
  display: block;
  height: 30px;
  width: 100%;
  position: absolute;
  bottom: 0;
}
