.ownership-about {
	.image-description-container {
		.image-description-parent {
			min-height: 450px;
			.text-content {
				padding: 1rem;
			}
		}
	}
	&-element {
		padding: 3rem 2rem 2rem $padding-copy-left;
		@extend .ownership-col;
		&-title {
			@extend .ownership-body-title-text;
			padding-bottom: 2rem;
			padding-left: 0;
		}
		&-content {
			padding: 2rem 2rem 0 2rem;
			.item {
				padding-bottom: 1rem;
				.header {
					font-size: 18px;
					color: $secondary-color;
					font-weight: bold;
					text-transform: uppercase;
					@include tracking-to-spacing(100);
				}
				.description {
					font-size: 16px;
				}
			}
		}
	}

	.image-col {
		margin: auto;
		min-height: 200px;
	}

	.break {
		&:after {
			@extend .header-underline;
			background-color: $secondary-color;
			height: 7px;
			width: 44px;
			top: -2px;
			left: $padding-copy-left;
		}
	}
}

@media only screen and (max-width: 767px) {
	.ownership-about-element {
		padding: 2rem 0;
	}
	.ownership-about-element-content {
		padding: 2rem 2rem 0 2rem;
	}
	.ownership-about-element-title {
		padding-left: 2rem;
	}
}

@media only screen and (min-width: 768px) and (max-width: 1199px) {
	.ownership-about-element-content {
		padding: 2rem 2rem 0 0;
	}
}
