.title-header {
  margin-bottom: 34px;
}

.destinations {
  &-center-header {
    @extend .section-header;
    text-align: center;
  }
  &-center-header ~ p {
    padding: 0 1rem;
  }
  &-rewards {
    &-description {
      text-align: center;
      padding: 2rem 15%;
      .general-info-link {
        text-transform: uppercase;
      }
    }
  }
  &-container {
    padding: 1.5rem 0.5rem;
    width: 100%;

    @media (min-width: 768px) {
      padding: 4rem 2rem;
    }
    .title {
      text-align: center;
      & ~ p {
        text-align: center;
      }
    }
    &-explore {
      @extend .destinations-container;
      [class*="col-"] {
        margin-bottom: 4rem;
      }
    }
    &-experiences {
      @extend .destinations-container;
      padding: 2rem 0 0 0;
      > .container-fluid {
        padding: 0;
      }
      .title {
        @extend .section-header;
        text-align: left;
      }
    }
    .image-description-parent {
      width: 100%;
	height: 100%;
	.image-container {
		position: relative;
    display: inline-block;
		&:before {
			position: absolute;
			top: 0.5rem;
			left: 0.5rem;
			width: calc(100% - 1rem);
			height: calc(100% - 0.5rem);
			border: 1px solid $gray-element;
			border-bottom: none;
			content: '';
		}
	}
      .text-content {
        display: inline-block;
        position: relative;
        padding-top: 1rem;
        padding-bottom: 3rem;
        padding-left: 2rem;
        padding-right: 2rem;
        width: 100%;
    min-height: 250px;
        &:before {
          position: absolute;
          top: 0;
          left: 0.5rem;
          width: calc(100% - 1rem);
          height: calc(100% - 0.5rem);
          border: 1px solid $gray-element;
          border-top: none;
          content: '';
        }
      }
      .description {
        margin-bottom: 3rem;
      }
    }
  }
  &-resorts {
    &-premium-items {
      padding-top: 2rem;
      [class*="col-"] {
        margin-bottom: 3rem;
      }
      .image-description-parent {
        display: inline-block;
        .marriott-btn {
          position: absolute;
          left: 50%;
          bottom: 0;
          transform: translate(-50%, 25%);
        }
      }
    }
    &-list {
      background-color: $milk-background;
      opacity: 0.99;
      .title {
        @extend .section-header;
        text-align: left;
      }
    }
    &-luxury {
      .destinations-resorts-item {
        &:first-child {
          padding-top: 1rem;
        }
      }
      &-title {
        margin-top: 3rem;
      }
    }
    &-dropdown-container {
      padding-top: 3rem;
      padding-left: 0;
      padding-right: 0;
    }
    &-item {
      padding-top: 4rem;

      a {
        text-decoration: none;
        &:hover {
          text-decoration: none;
          color: $white;
        }
      }
      .image-col {
        padding: 0;
      }
      .image-item {
        padding: 0;
        margin: auto;
        position: static;
        img {
          width: 100%;
        }
      }
      .content {
        padding: 0;
      }
      &-content {
        color: $copy-color;
        padding: 2rem;
        
        sup,
        .trademark {
          font-size: 50%;
        }
        .title {
          font-size: 26px;
          font-family: "Baskerville BT WGL4 W05 Roman", serif;
          display: inline;
          text-transform: none;
          color: $copy-color;
          letter-spacing: normal;
        }
        .location-title {
          padding-top: 0.5rem;
          margin-bottom: 1.5rem;
          color: $secondary-color;
          text-transform: uppercase;
          font-size: 16px;
          font-weight: bold;
          @include tracking-to-spacing(100);
        }
        .description {
          white-space: normal;
          a {
            @extend .general-info-link;
          }
        }
        .interests {
          padding-bottom: 2.5rem;
          display: flex;
          flex-direction: row;
          justify-content: flex-start;
          align-items: center;
          flex-wrap: wrap;
          .interests-container {
            margin-right: 1rem;
            &:last-child {
              margin-right: 0;
            }
          }
        }
        .resort-header-link {
          position: relative;
          top: -3px;
          left: 3px;
          font-size: 8px;
          line-height: 11.5px;
          text-decoration: none;
          &:hover {
            text-decoration: none;
            color: $white;
          }
        }
        .marriott-btn {
          white-space: nowrap;
          position: absolute;
          bottom: -0.5rem;
          font-size: 16px;
          z-index: 100;
        }
      }
    }
    @media (min-width: 576px) {
      &-item {
        padding-top: 4rem;
        .image-col {
          padding: 0;
        }
      }
    }
    @media (min-width: 992px) {
      &-item {
        padding-top: 4rem;
        .image-col {
          padding: 0;
        }
      }
    }
    @media (max-width: 1199px) {
      &-item {
        .image-parent {
          display: inline-block;
          position: relative;
          width: 100%;
          &:before {
            position: absolute;
            top: 0.5rem;
            left: 0.5rem;
            width: calc(100% - 1.0rem);
            height: calc(100% - 0.5rem);
            border: 1px solid $gray-element;
            border-bottom: none;
            content: '';
          }
        }
        .destinations-resort-item-column-content {
          .title {
            position: relative;
          }
          &:before {
            position: absolute;
            left: 0.5rem;
            right: 0.5rem;
            width: calc(100% - 1.0rem);
            height: calc(100% - 0.5rem);
            border: 1px solid $gray-element;
            border-top: none;
            content: '';
          }
        }
      }
    }
    @media (min-width: 1200px) {
      &-item {
        padding-top: 4rem;
        .image-parent {
          display: inline-block;
          position: relative;
          width: 100%;
          &:before {
            position: absolute;
            top: 0.5rem;
            left: 0.5rem;
            width: calc(100% - 0.5rem);
            height: calc(100% - 1rem);
            border: 1px solid $gray-element;
            border-right: none;
            content: '';
          }
        }
        .destinations-resort-item-column-content {
          &:before {
            position: absolute;
            top: 0.5rem;
            right: 0.5rem;
            width: calc(100% - 0.5rem);
            height: calc(100% - 1rem);
            border: 1px solid $gray-element;
            border-left: none;
            content: '';
          }
        }
        .image-item {
          padding: 0;
          margin: auto;
          position: relative;
          height: 100%;
          overflow: hidden;
          &-container {
            position: relative;
            height: 100%;
            width: 100%;
            min-height: 290px;
          }
          img {
            position: absolute;
            width: 150%;
            top: -9999px;
            bottom: -9999px;
            left: -9999px;
            right: -9999px;
            margin: auto;
            z-index: -1;
          }
        }
        &-content {
          .title {
            position: relative;
          }
          .marriott-btn {
            bottom: auto;
          }
        }
      }
    }
    &-overview {
      &-tripadvisor {
        justify-content: center;
        flex-direction: column;
        display: flex;
        margin-top: 1rem;
        img {
          margin-right: 2rem;
          &:last-child {
            margin-right: 0;
          }
        }
      }
      &-header {
        padding: 2rem 0 2rem 14rem;
        h1 {
          @include text(BaskervilleBt, 52px);
        }
        h2 {
          font-size: 16px;
        }
      }
      &-item {
        @extend .destinations-resorts-item;
        padding-top: 2rem;
        .container {
          height: 100%;
        }
        .row {
          height: 100%;
          @include outline($gray-element, 10px);
        }
        &-content {
          
          @extend .destinations-resorts-item-content;
          .title {
            font-size: 24px;
            font-weight: bold;
          }
          .subtitle {
            font-size: 12px;
            text-transform: uppercase;
            color: $secondary-color;
            padding-top: 0.5rem;
          }
          .description {
            padding-bottom: 1rem;
          }
          .marriott-btn {
            position: absolute;
            bottom: -0.5rem;
          }
        }
      }
    }
  }
}
.resort-preview-parent {
  position: relative;
  img {
    min-height: 215px;
  }
  .readmore-resort-description {
    overflow: hidden;
    margin-bottom: 1.5rem;
  }
}

.destinations-title {
  margin-bottom: 12px;
  margin-top: 20px;
}

#map_nav {
  opacity: 0;
  margin-top: 180px !important;

  @media (max-width: 500px) {
    margin-top: 200px !important;
  }
}

#map_controls {
  opacity: 0;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 1rem;
  display: none;
  width: 976px;
  font-size: 16px;

  @media (max-width: 797px) {
    width: 100%;

    #destinations-subheader {
      color: $secondary-color;
      font-size: 12px;
      font-weight: bold;
    }

    .destinations-toggle {
      display: block;
      float: none;
      width: 100%;
      margin: auto;
      margin-bottom: 10px;
    }
  }

  .subtitle {
    margin: auto;
    text-align: center;
  }

  .map-controls-toggles {
    white-space: nowrap;
    margin: auto;
  }

  .map-check {
    width: 12px;
    height: 12px;
    text-align: center;
    color: white;
    display: inline-block;
    margin-right: 10px;
    cursor: pointer;
    outline: 2px solid #7f7a75;
  }
}

.destinations-toggle {
  font-family: "Kessel_105 W05 Bold", sans-serif;
  font-weight: bold !important;
  font-size: 14px;
  color: #7f7a75;
  margin-right: 1rem;
  &:hover {
    cursor: pointer;
  }

  .map-check {
    font-family: Arial;
  }
}

.destinations-region {
  font-family: "Kessel_105 W05 Book", sans-serif;
  font-weight: bold;
  position: "absolute";
  color: "#333333";
  width: "150px";
  text-align: "center";
  transform: "translate(-40%, 0%)";
  white-space: "nowrap";
  font-size: "16px";
  font-weight: "bold";
}

.rewards-icons {
  &-one,
  &-two,
  &-three {
    display: none;
    padding-left: 1rem;
    padding-right: 1rem;
    img {
      display: block;
      margin: auto;
      width: 100%;
    }
  }
}
// Internet explorer 10 hack
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
	.destinations-title {
    margin-bottom: 11px;
    margin-top: 20px;
  }
}

@media only screen and (max-width: 767px) {
  .destinations-rewards-description {
    padding: 2rem 1rem;
  }
  .rewards-icons {
    &-one,
    &-two {
      display: none;
    }
    &-three {
      display: block;
    }
  }
}

@media only screen and (min-width: 768px) {
  
  .rewards-icons {
    &-three {
      display: none;
    }
    &-two {
      display: block;
    }
  }
}

@media only screen and (min-width: 768px) and (max-width: 1199px) {
	.destinations {
    &-center-header ~ p {
      padding: 0 3rem;
    }
  }
}

@media only screen and (max-width: 991px) {
  .resort-search-container {
    margin-bottom: 1rem;
  }
}

@media only screen and (min-width: 1200px) {
	.destinations {
    &-center-header ~ p {
      text-align: center;
    }
  }
  .rewards-icons {
    &-three,
    &-two {
      display: none;
    }
    &-one {
      display: block;
    }
  }
}

.ui-autocomplete {
  max-height: 290px;
  overflow-y: scroll;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  .ui-menu-item {
    &-wrapper {
      display: inline-block;
      width: 100%;
      &:hover,
      &:focus,
      &:active {
        text-decoration: none;
      }
    }
  }
  .ui-state-active {
    background: $secondary-color !important;
    color: $white;
    border-radius: 0;
    width: 100%;
  }
  .ui-state-disabled {
    color: $copy-color;
  }
}
