.home-banner {
	.row {
		padding-top: 50px;
	}

	.title {
		text-align: center;
		padding-left: 10%;
		padding-right: 10%;

		.title-header {
			font-size: 48px;
		}
	}
}

.title-description {
	font-size: 16px;
	padding-top: 30px;
	margin-bottom: 30px;
}

.skip {
	padding: 0 10px;
	position: fixed;
	top: 0;
	left: 0;
	background: #fff;
	z-index: 101;
	opacity: 0;
	transition: opacity .3s;

	&:focus {
		opacity: 1;
	}
}

.round-icon {
	border-radius: 50%;
}


.item-description {
	overflow: hidden;

	h4 {
		font-size: 24px;
	}

	p {
		font-size: 16px;
		overflow: hidden;
		white-space: normal !important;
		margin-bottom: 1.5rem;
	}
}

.action-list {
	list-style-type: none;

	.item-content {
		width: 100%;

		img {
			float: left;
			margin-right: 0.75rem;
		}
	}
}

.home-body-content {
	padding-top: 4rem;
	padding-bottom: 2rem;
}


$outline-item-offset: -0.5rem;

.fancy-outline-inset {
	position: relative;
	&:before {
		border: 1px solid $vertical-divider-color;
		top: -$outline-item-offset;
		left: -$outline-item-offset;
		right: -$outline-item-offset;
		bottom: -$outline-item-offset;
		position: absolute;
		content: "";
	}
}

.image-description-parent {
	width: 100%;
	height: 100%;
	.image-container {
		position: relative;
		display: inline-block;
		&:before {
			position: absolute;
			top: 0.5rem;
			left: 0.5rem;
			width: calc(100% - 1rem);
			height: calc(100% - 0.5rem);
			border: 1px solid $gray-element;
			border-bottom: none;
			content: '';
		}
	}
	.destinations-resorts-item-content {
		display: inline-block;
		min-height: 345px;
        position: relative;
        &:before {
          position: absolute;
          top: 0;
          left: 0.5rem;
          width: calc(100% - 1rem);
          height: calc(100% - 0.5rem);
          border: 1px solid $gray-element;
          border-top: none;
          content: '';
        }
	}
}

.image-description-container {
	padding: 0 0 2rem 0;
	[class*='col-'] {
		display: flex;
	}
	img {
		width: 100%;
		position: static;
	}
	.image-description-parent {
		a {
			text-decoration: none;
			&:hover {
				text-decoration: none;
				color: $white;
			}
			&.general-info-link,&.telephone-number {
				@extend .general-info-link;
				&:hover {
					text-decoration: underline;
				}
			}
		}
		.text-content {
			padding: 2rem 1.5rem 3rem 1.5rem;
			position: relative;
			word-wrap: break-word;
			h4 {
				font-size: 30px;
				font-family: "Baskerville BT WGL4 W05 Roman", serif;
			}
			p {
				font-size: 16px;
				white-space: normal !important;
			}
		}
		.image-description-button {
			position: absolute;
			display: inline-block;
			transform: translate(-50%, 25%);
			left: 50%;
			bottom: 0;
		}
	}
}

@media only screen and (max-width: 575px) {
	.image-description-container .image-description-parent .text-content h4 {
		font-size: 24px;
	}
	.action-list {
		padding-left: 0;
	}
	.home-banner {
		.row {
			padding-top: 1.5rem;
		}
		.title {
			padding-left: 1rem;
			padding-right: 1rem;
			&-header {
				h1 {
					font-size: 28px !important;
				}
			}
			&-description {
				padding-top: 1rem;
			}
		}
	}
}

@media only screen and (max-width: 767px) {
	.home-body-content {
		padding: 2rem 0;
	}
	.home-banner {
		.title {
			.title-header {
				margin-bottom: 0;
				& ~ .title-description {
					padding-left: 1rem;
					padding-right: 1rem;
				}
			}
		}
	}
	.vacation-greatness {
		padding: 1rem 2rem 1rem 2rem;
		max-width: 100% !important;
		&-content {
			padding: 0 2rem;
		}
	}
}

@media only screen and (max-width: 991px) {
	.image-description-container {
		[class*='col-'] {
			margin-bottom: 1.5rem;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}
