$body-max-width: 1400px;
$logoWidth: 160px;
$readMoreTransition: 0.5s;
// Global padding
$padding-copy-left: 2rem;
$padding-copy-left-inner-container: 1rem;
// Dropdown
$dropdown-box-padding-top-bottom: 0.5rem;
$dropdown-box-padding-left-right: 1rem;
$dropdown-left: calc(#{$dropdown-box-padding-left-right} * -0.5);
$dropdown-underline-top: calc(#{$dropdown-box-padding-top-bottom} * -0.5);
$dropdown-transition-time: 0.2s;
$slider-btn-next-width: 64px;
$slider-btn-next-height: 80px;
$slider-btn-icon-size: 30px;
$slider-caption-text-size: 85px;

$carouselCenterPaddingLeft: calc(#{$logoWidth} + 2rem);

$mobile-menu-break: 1130px;
$calendar-icon-size: 18px;
$calendar-icon-top-offset: calc(#{$calendar-icon-size} * 2);
$calendar-icon-offset: calc(#{$calendar-icon-size} + (#{$calendar-icon-size} / 2));

$fixedHeaderHeight: 65px;
