.resort-accomodations {
	position: relative;
	top: 6px;
	.villas-menu {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		align-content: center;
		flex-wrap: nowrap;
		background-color: $milk-background;
		padding-top: 2rem;
		padding-bottom: 3rem;
		&-title {
			text-transform: uppercase;
			font-size: 30px;
			padding-bottom: 2rem;
			color: $secondary-color;
			text-align: center;
			@include tracking-to-spacing(250);

			@media (max-width: 768px) {
					padding: 2rem;
					padding-left: 6rem;
					padding-right: 6rem;
					font-size: 18pt;
			}
		}
		.resort-dropdown {
			width: auto;
			min-width: 500px;

			@media (max-width: 768px) {
				min-width: auto !important;
				padding: 2rem;
				width: 90%;
				padding-top: 1rem;
				padding-bottom: 1rem;
				padding-left: 1rem;
			}
		}

		&-option {
			font-size: 16px;
			text-transform: uppercase;
			color: $white;
			flex: 1;
			text-align: center;
			padding-top: 0.5rem;
			padding-bottom: 0.5rem;
			border-right: 1px solid $resort-menu-divider-color;
			&:hover {
				text-decoration: none;
				@extend .resort-selection-active;
			}
		}

		@media (max-width: 768px) {
			width: 100%;
			padding: 0;
			min-width: auto;
			padding-bottom: 2rem;
		}
	}

	.villa-detail-container {
		padding-top: 2rem;

		@media (max-width: 768px) {
			width: 100%;
			padding: 0;
		}
	}

	.villa-content-container {
		padding-bottom: 5rem;
		padding-left: 0;
		padding-right: 0;
		.header-container {
			padding-top: 4rem;
			.vacation-text {
				padding-top: 0.5rem;
				padding-bottom: 2rem;
			}
		}
	}

	.villa-img-col {
		outline: 2px solid $gray-element;
	}

	.villa-carousel-container {
		width: 100%;
		position: absolute;
		top: 50%;
		left: 0;
		transform: translateY(-50%);
		img {
			width: 100%;
			&:focus {
				outline: none;
			}
		}
		.slick-slide {
			display: flex;
			padding: 3rem 6rem;
			&:focus {
				outline: none;
			}
			img {
				width: 100%;
				height: 100%;
				margin: auto;
				&:focus {
					outline: none;
				}
			}
		}
	}

	.villa-carousel-dots {
		bottom: 0.5rem;
		position: absolute;
		display: block;
		width: 100%;
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;

		li {
			margin: 0 5px;
			position: relative;
			display: inline-block;
			width: 12px;
			height: 12px;
			padding: 0;
			cursor: pointer;
			border-radius: 50%;
			border: solid $gray-element;
			border-width: 1px;
			&.dot-active {
				background-color: $secondary-color;
			}
		}
	}

	.villa-list {
		ul {
			list-style: none;
			margin: 0;
			padding: 1rem 0 0 0;
		}
		.villa-list-item {
			margin: 5px 0;
			line-height: 1.2;
			padding: 5px;
			@extend .vacation-text;
			.icon-caret-right {
				color: $secondary-color;
				position: relative;
				top: 2px;
			}
			&-alternate {
				background-color: $villa-list-item-alternate;
			}
		}
	}

	.villa-details {
		@include outline(#dadcdb, 30px, 1px);
		padding: 1rem;

		@media (max-width: 768px) {
			width: 100%;
			padding: 0;
			margin: 0;

			outline: none;
		}

		&-icon {
			float: left;
			@media (max-width: 768px) {
				margin-left: 2rem !important;
			}
		}

		&-copy {
			float: left;
			width: 70%;
		}

		&-row {
			padding: 4rem;

			@media (max-width: 768px) {
				padding: 2rem 0rem;
			}
		}

		&-header {
			font-weight: bold;
			font-size: 14pt;

			@media (max-width: 768px) {
				font-size: 12pt;
			}
			&-container {

				background-color: #f7f7f7;
				margin-bottom: 0px;
				max-height: 135px;
				height: 135px;
				padding-top: 1rem;
				position: relative;
				text-align: left;
				display: block;
							
				@media (max-width: 768px) {
					padding-top: 2rem;
				}
				
				&::before,
				  &-info {
				    display: inline-block;
				    vertical-align: middle;
				    width: 95%;
				  }

				&::before {
				    content: '';
				    width: 0;
				    height: 100%;
				  }
			}

			&-flavor-crystal {
				background-color: $secondary-color;
				width: 30px;
				height: 6px;
				margin-left: 20px;
				padding-top: 0;
				position: absolute;
				top: 151px;

				@media (max-width: 768px) {
					top: 135px;
					margin-left: 3rem;
				}
			}
		}
		&-details {
			padding: 1rem;
			padding-top: 1rem;
		}
	}
}
