.request-info {
	.ownership-body-element {
		padding-top: 0;
	}
	&-form {
		padding-top: 1rem;
		padding-bottom: 1rem;
	}
	.ownership-hero-info {
		padding: 4rem 0;
	}
	.marriott-btn {
		padding-left: 2rem;
		padding-right: 2rem;
	}
	.form-check, .form-check-label, .form-check-input {
		cursor: pointer;
		&:focus {
			outline: none;
		}
	}
	&-main {
		.title {
			margin-bottom: 1.0rem;
			@include tracking-to-spacing(75);
		}

		&-options {
			padding-bottom: 4rem;
			.form-check-inline {
				padding-right: 1rem;
			}
		}
	}
	&-owner {
		&-options {
			padding: 1rem 0;
			@extend .ownership-text;
			white-space: normal;
		}
	}
	#electionFormCheck {
		font-size: 12px;
	}
	.ownership-about-mvc {
		padding-top: 4rem;
		p {
			margin: 0;
			white-space: normal;
		}
		.body-content {
			padding-left: 2rem;
			padding-right: 2rem;
			.item-content {
				margin-bottom: 1rem;
			}
		}
	}
}

@media only screen and (max-width: 575px) {
	.request-info {
		.ownership-about-mvc {
			.body-content {
				padding-top: 1rem;
				padding-bottom: 0;
				.title-description {
					padding-top: 1rem;
				}
				p {
					padding-bottom: 1rem !important;
				}
			}
		}
		&-main-options {
			padding-bottom: 2rem;
		}
		&-form {
			padding-top: 0.5rem;
			padding-bottom: 0.5rem;
		}
		.ownership-about-mvc {
			padding-top: 1rem;
		}
		&-owner-options {
			.form-check {
				margin-bottom: 1rem;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
		.image-description-container {
			margin-top: 2rem;
		}
		.hero-element-info {
			&-header {
				padding-left: 0;
				padding-right: 0;
			}
			&-body {
				padding-left: 0;
				padding-right: 0;
			}
			.break {
				&:after {
					left: 0;
				}
			}
		}
	}
}

@media only screen and (max-width: 1199px) {
	.request-info {
		.hero-element-info {
			padding-bottom: 2rem;
		}
	}
}
