// Date picker styling
#ui-datepicker-div {
  border-radius: 0;
  padding: 0;
  font-family: "Kessel_105 W05 Book", sans-serif;
}

#specialCode {
  font-size: 16px;
  color: $primary-color;
}

// Day names
.ui-datepicker {
  table {
    font-size: 0.8em;
  }
  .ui-datepicker-prev,
  .ui-datepicker-next {
    top: 5px;
    cursor: pointer;
  }
  .ui-datepicker-prev-hover,
  .ui-datepicker-next-hover {
    border: none !important;
    background: none !important;
    top: 5px;
    right: 2px;
    text-decoration: none;
  }
  .ui-widget-header .ui-icon {
    font-family: "icomoon" !important;
    font-style: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    text-indent: 0px;
    background-image: none !important;
    font-size: 24px;
    width: auto;
    height: auto;

    &.ui-icon-circle-triangle-w {
      &:before {
        content: "\eb8a";
      }
    }
    &.ui-icon-circle-triangle-e {
      &:before {
        content: "\eb8b";
      }
    }
  }
  .ui-datepicker-header {
    background-color: $white;
    @extend .booking-form-element;
    border: none !important;
    border-radius: 0 !important;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background: none;
    background-color: $booking-calendar-header-bg !important;
  }

  .ui-datepicker-title {
    color: $white;
    text-align: center;
    font-size: 18px;
    @include tracking-to-spacing(50);
    text-transform: uppercase;
    font-weight: bold !important;
  }

  th {
    text-transform: uppercase;
    padding: 5px;
    color: $copy-color;
    font-weight: bold;
  }
  tbody td {
    padding: 5px;
    text-align: center;
  }

  td span,
  td a {
    display: inline-block;
    font-weight: bold;
    text-align: center;
    width: 30px;
    height: 30px;
    color: $copy-color;
  }
  // states for dates in calendar
  &-calendar {
    // default state
    .ui-state {
      &-default {
        background: $white;
        border: 0;
        font-weight: bold;
      }
      &-highlight {
        color: $copy-color;
      }
      // hover state
      &-hover {
        background: $secondary-color;
        color: $white;
      }
      // active state
      &-active {
        background: $secondary-color;
        color: $white;
        border: 1px solid $white;
        position: relative;
        margin: -1px;
      }
    }
  }
}

// END Date picker

.booking-form-element {
  font-size: 16px;
  color: $primary-color;
  &:focus {
    border-color: $white;
    box-shadow: none;
  }
}

.calendar {
  &-links {
    padding: 0 0 0 1rem;
  }
  .form-control[readonly] {
    background-color: $white;
  }
  .ui-datepicker {
    width: auto;
  }
  .header-text {
    text-align: center;
  }

  .btn-toolbar {
    flex-wrap: nowrap;
    background-color: $gray-option-hover;
    a {
      font-size: 16px;
      color: $white;
      text-transform: uppercase;
      span {
        color: $white;
      }
    }

    .book-btn {
      padding: 1rem 2rem;
      background-color: $copy-color;
      border-radius: 0;
      @include tracking-to-spacing(100);
      &.book-btn-active {
        background-color: $secondary-color;
        border-bottom: 1px solid $white;
        box-shadow: none;
      }
    }
  }

  .calendar-body {
    background-color: $secondary-color;
    border-radius: 0;
    padding: 1rem;
    @extend .labels-text;
    select {
      option {
        &:focus {
          outline: none;
        }
      }
    }
    .custom-checkbox {
      cursor: pointer;
      padding-left: 1.3rem;
      .custom-control-input {
        &:focus {
          outline: none;
        }
      }
      .custom-control-label {
        cursor: pointer;
        &:before {
          background: none;
          border: solid $white 1px;
          border-radius: 0;
          width: 0.75rem;
          height: 0.75rem;
          box-shadow: none;
        }
        &:after {
          top: 0.1rem;
          left: -0.1rem;
        }
      }
    }

    .custom-control {
      &:focus {
        outline: none;
      }
      &-description {
        @extend .labels-text;
        cursor: pointer;
      }
    }

    a {
      @extend .labels-text;
      text-decoration: underline;
      &:hover {
        text-decoration-color: $white;
        text-decoration-line: underline;
      }
    }
  }

  .corporate-code-input {
    margin-top: 1rem;
  }

  .btn-booking-submit {
    text-align: center;
    border: solid $white 1px;
    font-weight: normal;
    font-size: 24px;
    border-radius: 0;
    text-transform: uppercase;
    width: 100%;
    margin-top: 30px;
    appearance: none;
    cursor: pointer;
  }

  .ui-datepicker-trigger {
    position: absolute;
    margin: 0;
    padding: 0;
    font-size: $calendar-icon-size;
    color: $icon-gray;
    top: $calendar-icon-top-offset;
    right: $calendar-icon-offset;
    background: none;
    appearance: none;
    border: none;
    cursor: pointer;
    font-family: "icomoon" !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    &:before {
      content: "\f43d";
    }
  }

  .form-control {
    &::placeholder {
      color: $booking-form-placeholder;
      opacity: 0.5;
    }
  }
}
@media only screen and (max-width: 575px) {
  .calendar {
    .btn-booking-submit {
      margin-top: 1rem;
    }
    .book-btn {
      white-space: normal;
    }
  }

  .ui-datepicker {
    .ui-datepicker-header {
      padding-top: 0.3rem;
      padding-bottom: 0.3em;
      .ui-icon {
        font-size: 16px;
      }
    }
    .ui-datepicker-title {
      font-size: 12px;
    }
    .ui-datepicker-prev,
    .ui-datepicker-next {
      top: 2px;
    }
    table {
      font-size: 0.7rem;
    }
    tbody {
      td {
        padding: 0;
        a,
        span {
          width: 22px;
          height: 22px;
        }
      }
    }
  }
}
@media only screen and (min-width: 576px) and (max-width: 767px) {
  .ui-datepicker {
    .ui-datepicker-header {
      padding-top: 0.3rem;
      padding-bottom: 0.3em;
    }
    .ui-datepicker-title {
      font-size: 16px;
    }
    .ui-datepicker-prev,
    .ui-datepicker-next {
      top: 0px;
    }
  }
}

@media only screen and (max-width: 767px) {
  .calendar {
    padding-top: 2rem;
    .book-btn {
      position: relative;
      i {
        display: inline-block;
        position: relative;
        top: 2px;
        transition: all 0.1s linear;
      }
      span {
        color: $white;
        position: absolute;
        top: 50%;

        transform: translate(-50%, -50%);

      }
      &-up {
        i {
          transform: scale(1, -1);
        }
      }
      &:not(.book-btn-up) {
        border-bottom: none !important;
      }
    }
    &-body {
      position: relative;
      max-height: 0;
      transition: max-height 0.3s ease;
      padding: 0 !important;
      form {
        visibility: hidden;
        opacity: 0;
        transition: opacity 0.3s ease;
        > .container {
          .row {
            &:last-child {
              margin-top: 2rem;
            }
          }
        }
      }
      &-active {
        position: relative;
        top: 0;
        padding: 1rem !important;
        height: auto !important;
        max-height: 800px;
        padding: 1rem !important;
        form {
          visibility: visible;
          opacity: 1;
        }
        
      }
    }
  }
}

@media only screen and (min-width: 768px) {
  .booking-form-element {
    font-size: 16px;
    color: $primary-color;
  }
  .book-btn {
    i {
      display: none;
    }
  }
}
@media only screen and (min-width: 992px) {
  .booking-form-element {
    color: $primary-color;
  }
}
@media only screen and (min-width: 1030px) {
}
