.amenities-list {
  &-container {
    padding-top: 3rem;
  }
  &-header {
    font-size: 30px;
    text-transform: uppercase;
    font-family: "Kessel_105 W05 Book", sans-serif;
    @include tracking-to-spacing(100);
    color: $secondary-color;
    padding-left: 2rem;
    padding-top: 2rem;
    &.header-underline {
      &:after {
        bottom: -20px;
      }
    }
  }
  &-content {
    padding-top: 1rem;
    padding-left: 2rem;
    padding-bottom: 2rem;
    .features-container {
      justify-content: flex-start;
    }
    .resort-icon-description-item {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
    .amenities-item-header {
      max-height: 150px;
      cursor: pointer;
      .feature-icon {
        &-content {
          width: auto;
        }
        &-img {
          position: relative;
          width: auto;
          display: inline-block;
          left: 50%;
          transform: translateX(-50%);
        }
        &-text {
          display: inline-block;
          left: 50%;
          transform: translateX(-50%);
          h4 {
            font-size: 12px;
            color: $copy-color;
            text-transform: uppercase;
            margin: 0;
            @include tracking-to-spacing(150);
            font-family: "Kessel_105 W05 Book", sans-serif;
            display: inline-block;
            position: relative;
          }
          i {
            display: inline-block;
            position: absolute;
            top: 2px;
            right: -1.2rem;
            transition: all 0.1s linear;
          }
        }
      }
      &-up {
        .feature-icon-text {
          i {
            transform: scale(1, -1);
          }
        }
      }
    }
    .amenities-item-content {
      display: none;
    }
    ul {
      margin: 0;
      padding: 1rem 0 0 0;
      li {
        color: $secondary-color;
        span {
          font-size: 14px;
          color: $primary-color;
        }
      }
    }
  }
}

.amenities-detail {
  padding-left: 2rem;
  margin-bottom: 2rem;
  display: none;
  position: relative;
  .resort-icon-description-item-header {
    font-size: 24px;
    margin-bottom: 1rem;
  }
}

@media only screen and (max-width: 767px) {
  .amenities-list {
    &-header {
      padding-left: 0;
    }
    &-content {
      padding-left: 0;
      padding-right: 0;
    }
  }
}

@media only screen and (min-width: 768px) {
  .amenities-detail {
    display: block;
  }
}
