.resort-map-container {
	position: relative;
	.map-frame {
		width: 100%;
		min-height: 650px;
	}
	.resort-map-image {
		position: relative;
		top: 0;
		left: 0;
		width: 100%;
		padding: 1rem 1rem 4rem 1rem;
		border-bottom: 1px solid $gray-element;
		img {
			display: block;
			position: relative;
			top: 50%;
			left: 50%;
			max-height: 100%;
			transform: translate(-50%, -50%);
		}
	}
}

.transportation {
	width: 100%;
	margin: 0;
	padding-top: 2rem;
	padding-left: 4rem;
	padding-right: 3rem;
	text-align: center;
	&-header {
		font-size: 48px;
		&-info {
			font-size: 16px;
			padding-top: 1rem;
		}
	}
	&-options {
		&-menu {
			padding-top: 5rem;
			padding-bottom: 1rem;
			margin-left: auto;
			margin-right: auto;
			display: flex;
			justify-content: space-evenly;
			align-items: center;
		}
		&-item {
			cursor: pointer;
			color: $transportation-icon;
			font-size: 19px;
			font-weight: bold;
			text-transform: uppercase;
			&:hover {
				color: $transportation-icon-active;
			}
			i {
				font-size: 50px;
			}
		}
		&-title {
			padding-top: 1rem;
		}
	}
	&-info {
		text-align: left;
		width: 100%;
		padding: 4rem 1rem;
		
		&-header {
			font-size: 36px;
		}
		
	}
}


.resort-map-button {
	position: absolute;
	left: 50%;
	bottom: 0;
	transform: translate(-50%, -50%);
}

.resort-icon-description {
	&-container {
		height: 100%;
		&-row {
			margin-top: 2rem;
			&:first-child {
				margin-top: 0;
			}
		}
	}
	&-item {
		margin-top: 1rem;
		[class*=col-] {
			padding: 0;
		}
		
		.row {
			min-height: 150px;
			height: 100%;
			.amenities-item-header {
				position: relative;
				display: inline-block;
				&:before {
					position: absolute;
					top: 10px;
					left: 10px;
					width: calc(100% - 20px);
					height: calc(100% - 20px);
					border: 1px solid $gray-element;
					content: '';
				}
				&-up {
					position: relative;
					display: inline-block;
					&:before {
						border-bottom: none;
						height: calc(100% - 10px);
					}
				}
			}
			.amenities-item-content {
				position: relative;
				&:before {
					position: absolute;
					top: 0;
					left: 10px;
					width: calc(100% - 20px);
					height: calc(100% - 10px);
					border: 1px solid $gray-element;
					border-top: none;
					content: '';
				}
			}
		}
		&-header {
			font-size: 18px;
			color: $secondary-color;
			margin: 0;
			text-transform: uppercase;
		}
		&-content {
			font-size: 16px;
			padding: 1rem 1rem 1rem 2rem;
			display: block;
			margin: auto;

			p {
				white-space: normal;
				margin-top: 1rem;
			}
			&-list, ul {
				list-style: none;
				margin-top: 1rem;
				color: $secondary-color;
				padding-left: 1rem;
				.text, li{
					color: $copy-color;
				}
				li {
					&:before {
						color: $secondary-color;
						content: "\2022";
						font-size: 1rem;
						position: relative;
						top: 0;
						left: -1rem;
					}
				}
			}
		}
	}
}



@media only screen and (max-width: 575px) {
	.resort-icon-description {
		&-item {
			.feature-icon {
				&-img {
					min-width: 30px;
				}
			}
		}
	}
	.transportation {
		padding-left: 1rem;
		padding-right: 1rem;
		&-header {
			font-size: 36px;
		}
		&-info {
			padding-top: 2rem;
		}
	}
}

@media only screen and (max-width: 767px) {
	.transportation-info-item {
		&-header {
			margin-top: 1rem;
		}
	}
}

@media only screen and (max-width: 991px) {
	.transportation-info-item {
		margin-top: 1rem;
	}
}
