.header-text {
  font-size: 18px;
  color: $secondary-color;
  text-transform: uppercase;
  font-weight: bold;
  @include tracking-to-spacing(150);
}

.title-text {
  font-size: 42px;
  line-height: 1;
  font-family: "Baskerville BT WGL4 W05 Roman", serif;
}

.vacation-text {
  font-size: 16px;
}

.resort-selection-active {
  background-color: $gray-option-hover;
  box-shadow: none;
  color: $white;
}

.features {
  &-title {
    font-size: 18px;
    color: $secondary-color;
    text-transform: uppercase;
    text-align: center;
    @include tracking-to-spacing(150);
    margin-top: 2rem;
  }
  &-content {
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
  &-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  &-item {
    flex: 0 1 auto;
    min-width: 150px;
    min-height: 150px;
    margin: 3px;
    @extend .features-item-common;
    .feature-icon {
      &-content {
        top: 40%;
      }
      &-text {
        position: absolute;
        top: calc(100% + 1.5rem);
        left: 50%;
        transform: translateX(-50%);
        margin: 0;
      }
    }
  }
}


.resort-info {
  padding-left: 13.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  position: relative;
  .resort-header {
    h1 {
      font-size: 24px;
      color: $primary-color;
      display: inline-block;
      margin: 0;
    }
    &-link {
      position: relative;
      top: -2px;
      left: 5px;
    }
  }
  .resort-header-actions {
    &:first-child {
      margin-left: 0;
    }
    .resort-action {
      display: inline-block;
      font-size: 12px;
      color: $secondary-color;
      text-transform: uppercase;
      margin: 0 5px;
      position: relative;
      top: 3px;

      &:first-child {
        margin-left: 0;
      }
      i {
        font-size: 16px;
      }
      &-divider {
        &.ta-container-review {
          visibility: hidden;
          width: 208px;
          height: 20px;
          top: 2px;
          display: inline-block;
          a {
            position: relative;
            top: -2px;
            img {
              position: relative;
              top: -2px;

              & + span {
                font: 12px Arial,sans-serif;
                text-decoration: underline;
                color: $secondary-color;
              }
            }
          }
        }
        &:last-child {
          &:after {
            content: none;
          }
        }
        &:after {
          content: "";
          height: 20px;
          width: 1px;
          background-color: $vertical-divider-color;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: -0.5rem;
        }
      }

      .icon-sun-rise {
        font-size: 24px;
        position: relative;
        top: 3px;
      }

      &-link {
        color: $secondary-color;
        text-decoration: underline;
        vertical-align: 2px;
      }
      &-weather {
        font-size: 14px;
        color: $primary-color;
        vertical-align: 2px;
      }
    }
  }
}

.labels-text {
  font-size: 13px;
  color: $white;
}

.map-btn {
  @include button(18px);
  padding: 0.5rem 2rem;
  margin-top: 1rem;
}

@media screen and (min-width: $mobile-menu-break) {
  .resort-info {
    padding-left: calc(13.5rem + 15px);
  }
  #submenu-desktop {
    display: block;
  }
  #submenu-mobile {
    display: none;
  }
}
@media screen and (max-width: $mobile-menu-break) {
  .resort-header-container {
    padding: 0;
    .resort-info {
      padding: 1rem;
    }
  }
  #submenu-desktop {
    display: none;
  }
  #submenu-mobile {
    display: block;
    left: 0;
  }
}
