.ownership-started {
	&-container {
		@extend .ownership-col;
		padding: 4rem 0;
		position: relative;
		.info-text {
			@extend .ownership-text;
			white-space: normal;
			padding: 2rem 1rem;
		}
		&-text {
			padding-top: 2rem;
			p {
				margin: 0;
				white-space: normal
			}
		}
	}
}

@media only screen and (max-width: 767px) {
	.ownership-started-container {
		padding-top: 2rem;
		padding-bottom: 1rem;
		[class*='col-'] {
			.round-number {
				margin-top: 2rem;
			}
			&:first-child {
				.round-number {
					margin-top: 0;
				}
			}
			&:nth-child(2) {
				border-top: 1px solid $gray-element;
				border-bottom: 1px solid $gray-element;
			}
		}
		&-text {
			padding: 0 1rem;
		}
	}
}

@media only screen and (min-width: 768px) {
	.ownership-started-container {
		[class*='col-'] {
			padding: 0 1rem;
			&:nth-child(2) {
				border-left: 1px solid $gray-element;
				border-right: 1px solid $gray-element;
			}
		}
		&-text {
			padding: 0 1rem;
		}
	}
}
