@import "mixins";

html,
body {
  margin: 0;
  padding: 0 !important;
  height: 100%;
  width: 100%;
  font-size: 100%;
  max-width: $body-max-width;
  margin-left: auto;
  margin-right: auto;
}

a,
.btn {
  &:focus {
    outline: none;
    box-shadow: none;
  }
}

body {
  font-family: Arial, sans-serif;
  color: $copy-color;
}
button {
  &:focus {
    outline: none;
  }
}

@keyframes pulse_animation {
  0% { transform: scale(1); }
  30% { transform: scale(1); }
  40% { transform: scale(1.4); }
  50% { transform: scale(1); }
  60% { transform: scale(1); }
  70% { transform: scale(1.4); }
  80% { transform: scale(1); }
  100% { transform: scale(1); }
}

.pulse {
  animation-name: pulse_animation;
  animation-duration: 2000ms;
  transform-origin: 50% 50%;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}



h1 {
  font-family: "Baskerville BT WGL4 W05 Roman", serif;
  color: $primary-color;
}

h2,
h3,
h4,
h5,
h6 {
  font-family: "Kessel_105 W05 Book", sans-serif;
  color: $primary-color;
}

p {
  white-space: pre-line;
}

.image-120 {
  width: 120%;
}
.image-100 {
  width: 100%;
}
.image-description-border {
  @include outline($gray-element, 0.5rem);
  .image-description-button {
    z-index: 100;
  }
  img.img-fluid{
  	width: 100%;
  }
}

.section-header {
  font-family: "Kessel_105 W05 Book", sans-serif;
  font-size: 32px;
  color: $secondary-color;
  text-transform: uppercase;
  @include tracking-to-spacing(250);
  text-align: left;
}

.subsection-header {
  font-family: "Baskerville BT WGL4 W05 Roman", serif;
  color: $copy-color;
}

.btn {
  font-family: "Kessel_105 W05 Book", serif;
  font-weight: bold;
}

.marriott-btn {
  @extend .btn;
  @include button(12px, bold);
  @include tracking-to-spacing(50);
  cursor: pointer;
  &.no-hover {
    background-color: $secondary-color;
  }
  &:not(.no-hover):hover {
    color: $button-text-color;
    background-color: $button-hover-color;
  }
}

.vacation-greatness {
  padding-top: 3em;
  padding-bottom: 3em;
  p {
    white-space: normal;
  }
  &-header {
    font-size: 60px;
    padding-top: 1rem;
    font-family: "Baskerville BT WGL4 W05 Roman", serif;
  }
  &-subheader {
    font-size: 18px;
    color: $secondary-color;
    text-transform: uppercase;
    @include tracking-to-spacing(150);
  }
  &-content {
    font-size: 16px;
    padding: 0 9rem;
  }
}

.marriott-modal {
  z-index: 10000;
  top: 50%;
  transform: translateY(-50%);
  
  .modal-content {
    border-radius: 0;
    border: 4px solid $secondary-color;
  }
  .modal-footer {
    border-top: none;
    justify-content: center;
    padding-top: 0;
  }
}

.telephone-number {
  font-weight: bold;
  text-decoration: none;
  color: $secondary-color;
  font-size: inherit;
  &:hover {
    color: $secondary-color;
  }
}
.marriott-title-copy {
  color: $secondary-color;
  text-transform: uppercase;
}

// olapic
.olapic {
  .modal .modal-header .close {
    margin: 5px 5px 5px auto;
  }
  
  .main {
    a:hover {
      background: $secondary-color !important;
    }
  }

  .grid.olapic-wall {
    text-align: center;

    li.item {
      float: none;
      display: inline-block;
    }
  }
}

.olapic-container {
  background-color: #f5f5f5;
}

.header-underline {
  &:after {
    background-color: $gray-element;
    bottom: -10px;
    display: block;
    content: "";
    height: 1px;
    position: relative;
    width: 70px;
  }
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  img {
    width: 100%;
  }
  &-container {
    position: relative;
    width: 100%;
    &:before {
      content: "";
      display: block;
      padding-top: 100%;
    }
  }
}

.force-4-3-ratio {
  @include aspect-ratio(4, 3);
  overflow: hidden;
}

.hero-element {
  background-color: $hero-element-bg;
  [class*="col-"] {
    padding: 0;
  }
  .image-col {
    margin: auto;
    img {
      width: 100%;
    }
  }
  .cover-picture-container {
    @include aspect-ratio(4, 3);
  }
  &-info {
    &-header {
      font-size: 40px;
      padding-bottom: 2rem;
      padding: 0 1rem 2rem $padding-copy-left-inner-container;
    }
    .break {
      &:after {
        @extend .header-underline;
        background-color: $secondary-color;
        height: 7px;
        width: 44px;
        top: -1px;
        left: $padding-copy-left-inner-container;
      }
    }
    &-body {
      padding: 2rem 1rem 0 $padding-copy-left-inner-container;
      .title {
        font-size: 18px;
        text-transform: uppercase;
        color: $secondary-color;
        padding-top: 2rem;
        font-weight: bold;
        @include tracking-to-spacing(150);
        &:first-child {
          padding-top: 1rem;
        }
      }
      p {
        white-space: pre-line;
        margin: 0;
        padding: 0 1rem 0 0;
      }
      .btn {
        margin-top: 1rem;
      }
    }
  }
}

.trademark {
  font-size: 30%;
  vertical-align: super;
  margin: 0;
  padding: 0;
}

.trademark-medium {
  @extend .trademark;
  font-size: 50%;
}

.break {
  hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid $gray-element;
    margin: 0;
    padding: 0;
  }
}

.vertical-divider {
  position: absolute;
  border-right: 1px solid $gray-element;
  height: 100%;
}

.round-number {
  background-color: $secondary-color;
  font-size: 30px;
  color: $white;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin: auto;
  padding-top: 7px;
  text-align: center;
}

.image-fill {
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    min-width: 50%;
    min-height: 50%;
  }
}

.resort-dropdown {
  cursor: pointer;
  color: $copy-color;
  font-size: 22px;
  width: 100%;
  padding: 0.5rem 3rem 0.5rem 1rem;
  border-radius: 3px;
  appearance: none;
  background-repeat: no-repeat;
  background-position-x: 98%;
  background-position-y: center;
  white-space: normal;
  &:focus {
    outline-color: $secondary-color;
  }
  &::-ms-expand {
    display: none;
  }
}

.resort-search {
  &-container {
    display: flex;
    justify-content: space-between;
    .marriott-btn {
      flex: 1 1 10%;
      align-self: center;
      margin-left: 0.5rem;
    }
  }
  font-size: 22px;
  color: $copy-color;
  white-space: normal;
  width: auto;
  flex: 1 1 90%;
  padding: 0.5rem 1rem 0.5rem 0.5rem;
  &:focus {
    outline-color: $secondary-color;
  }
  &::-ms-expand {
    display: none;
  }
}

.owneronly {
  display: none;
}

.video-banner {
  position: relative;

  img {
    width: 100%;
  }

  .banner-image-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    color: $white;
    font-size: 40px;
    font-weight: bold;
    line-height: 1em;
    padding: 0 15%;
  }
}

.video-thumbnail {
  cursor: pointer;
}

.cover-picture {
  position: absolute;
  left: -9999px;
  right: -9999px;
  top: -9999px;
  bottom: -9999px;
  margin: auto;
  max-width: 100%;
  min-height: 100%;
  &-container {
    position: relative;
    overflow: hidden;
    padding: 0 !important;
    margin: 0 !important;
  }
}

.center-container {
  position: absolute;
  width: 70%;
  left: 15%;
}

.content {
  &-container {
    width: 100%;
    height: 100%;
    position: relative;
  }
  &-flavor {
    &-left {
      width: 50%;
      position: absolute;
      left: 0;
      top: 0;
      max-width: 300px;
      img {
        width: 100%;
      }
    }
    &-right {
      width: 50%;
      position: absolute;
      top: 0;
      right: 0;
      max-width: 300px;
      img {
        width: 100%;
      }
    }
  }
}

@media only screen and (max-width: 360px) {
  .section-header {
    font-size: 20px;
  }
}

@media only screen and (min-width: 361px) and (max-width: 575px) {
  .vacation-greatness {
    padding-left: 0 !important;
    padding-right: 0 !important;
    &-header {
      font-size: 40px;
    }
  }
  .section-header {
    font-size: 26px;
  }
}

@media only screen and (max-width: 575px) {
  .hero-element-info {
    padding: 2rem 1rem 2rem 1rem;
  }
  #vacation-style-info {
    padding: 2rem 0;
  }
}

@media only screen and (max-width: 1199px) {
  .hero-element {
    .cover-picture-container {
      &:before {
        content: "";
        padding: 0;
        width: 0;
      }
      > .content {
        position: static;
        width: 100%;
        height: auto;
        min-height: 0;
        max-width: 100%;
        min-width: 0;
      }
    }
  }
}

@media only screen and (min-width: 576px) and (max-width: $mobile-menu-break) {
  .hero-element-info {
    padding: 2rem 1rem 1rem $padding-copy-left;
  }
}

@media only screen and (min-width: $mobile-menu-break) {
  .hero-element-info {
    padding: 8rem 0 3rem $padding-copy-left-inner-container;
  }

}

