.search {
	&-result {
		div, p {
			margin: 0;
			padding: 0;
		}
		&-pagination {
			.marriott-page-item {
				padding: 0.1rem 0.5rem;
				margin-right: 3px
			}
		}
		margin-bottom: 2rem;
	}
	&-menu {
		&-container {
			margin: auto;
			height: 30px;
			&.active {
				.search-menu-input {
					opacity: 1;
					&-container {
						width: 110px;
						height: 25px;
						transition: all .5s cubic-bezier(0.000, 0.105, 0.035, 1.570);
						.search-icon {
							right: 0;
						}
					}
				}
			}

		}
		&-input {
			opacity: 0;
			position: absolute;
			left: 0;
			width: 100%;
			height: 100%;
			margin: 0;
			transition: all .3s cubic-bezier(0.000, 0.105, 0.035, 1.570);
    	transition-delay: 0.3s;
			padding: 1px 1.5rem 0 0.5rem;
			outline: none;
			font-family: "Kessel_105 W05 Book", serif;
			color: $primary-color;
			font-size: 14px;
			text-transform: uppercase;
			@include tracking-to-spacing(100);
			&-container {
				width: 25px;
				height: 25px;
				position: relative;
				overflow: hidden;
				transition: all 0.3s ease-in-out;
				.search-icon {
					&:hover {
						color: $secondary-color;
					}
					background: none;
					appearance: none;
					color: $primary-color;
					width: 25px;
					height: 25px;
					border: none;
					position: relative;
					padding: 0;
					margin: 0;
					cursor: pointer;
					z-index: 10;
					float: right;
					top: 1px;
					transition: all 0.3s ease-in-out;
				}
			}
		}
	}
}
