.ownership-about-mvc {
	.body-content {
		padding: 2rem $padding-copy-left 0 $padding-copy-left;
		.marriott-btn {
			margin-bottom: 2rem;
		}
	}
	.video-frame {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		display: none;
	}
	.video-banner {
		&:before {
			display: block;
			content: "";
			width: 100%;
			padding-top: (9 / 16) * 100%;
		}
		img {
			position: absolute;
			top: 0;
			left: 0;
		}
		&-content {
			position: absolute;
			width: 100%;
			top: 35%;
			z-index: 10;
			color: $white;
			cursor: pointer;
			
			&-title {
				position: relative;
				font-family: 'Baskerville BT WGL4 W05 Roman', serif;
				font-weight: bold;
				
				text-align: center;
				left: 50%;
				transform: translate(-50%, -50%);
				color: $white;
				text-shadow: 0px 1px 20px rgba(1, 1, 1, 1);
				font-size: 20px;
			}
			&-icon {
				position: relative;
				top: -1rem;
				left: -0.2rem;
				font-size: 8px;
				border: 1px solid rgba(255,255,255,0.5);
				padding: 0.5rem 1.5rem 0.5rem 1.5rem;
				border-radius: 90px;
				display: inline;
			}
		}
	}
	
	.kessel-header {
		@extend .section-header;
	}
	.image-description-container {
		.marriott-btn {
			margin: 0;
		}
		[class*='col-'] {
			margin-bottom: 2rem;
		}
		.image-description-parent {
			min-height: 490px;
		}
	}
	@media only screen and (min-width: 400px) and (max-width: 575px) {
		.video-banner-content {
			&-title {
				font-size: 28px;
			}
			&-icon {
				top: -1rem;
				left: -0.2rem;
				font-size: 8px;
				padding: 1rem 2rem 1rem 2rem;
			}
		}
	}
	@media only screen and (min-width: 576px) and (max-width: 767px) {
		.video-banner-content {
			&-title {
				font-size: 36px;
			}
			&-icon {
				font-size: 12px;
				padding: 1rem 2.5rem 1rem 2.5rem;
			}
		}
	}

	@media only screen and (max-width: 767px) {
		.home-banner {
			padding: 0;
			.row {
				padding-top: 20px;
			}
			.title {
				padding: 0;
			}
		}
		
	}

	@media (min-width: 580px) {
		.image-description-container {
			[class*='col-'] {
				margin-bottom: 2rem;
			}
			.image-description-parent {
				min-height: 550px;
			}
		}
	}
	@media (min-width: 768px) {
		.body-content {
			.row {
				[class*='col-'] {
					&:first-child {
						padding-bottom: 0;
					}
				}
			}
		}
		.image-description-container .image-description-parent {
			min-height: 560px;
		}
		
	}

	@media (min-width: 768px) and (max-width: 1200px) {
		.video-banner-content {
			&-title {
				font-size: 48px;
			}
			&-icon {
				font-size: 18px;
				left: -0.5rem;
				padding: 1rem 3rem 1rem 3rem;				
			}
		}
	}

	@media (min-width: 1200px) {
		.video-banner-content {
			&-title {
				font-size: 60px;
			}
			&-icon {
				top: -2rem;
				left: -0.5rem;
				font-size: 24px;
				padding: 1rem 3rem 1rem 3rem;				
			}
		}
	}

	@media (min-width: 990px) {
		.image-description-container .image-description-parent {
			min-height: 500px;
		}
	}
}
