.notfound {
  &-container {
    position: relative;
    margin: 0;
    padding: 0;
  }

  &-copy {
    position: absolute;
    left: 50%;
    top: 35%;
    transform: translate(-50%, -50%);

    h1 {
      font-family: "Kessel_105 W05 Book";
      font-size: 14pt;
      color: white;
      text-shadow: 0px 0px 19px rgba(0, 0, 0, 0.8);
    }

    p {
      color: white;
      font-size: 8pt;
      text-shadow: 0px 0px 19px rgba(0, 0, 0, 0.8);
    }

    a {
      color: white;
      text-decoration: underline;
    }
  }
}

@media (max-width: 767px) {
  .notfound {
    &-copy {
      top: 35%;
    }
  }
}

@media (min-width: 1040px) {
 .notfound {
    &-copy {
      h1{
        font-size: 25pt;
      }

      p{
        font-size: 14pt;
      }
    }
  }
 }