

.footer-container {
	padding: 2rem 2rem 1rem 2rem;

	h4 {
		font-size: 12px;
		text-transform: uppercase;
		font-weight: bold;
		color: $secondary-color;
		@include tracking-to-spacing(100);
	}

	.marriott-btn {
		white-space: normal;
	}
}

.social-icons {
	width: 100%;
	margin-top: 1.75rem;
	margin-bottom: 1rem;

	&:first-child {
		margin-left: 0;
	}

	a {
		&:hover {
			text-decoration: none;

			i {
				color: $secondary-color;
			}
		}
	}

	i {
		margin-left: 3px;
		margin-right: 3px;
		font-size: 19px;
		color: $copy-color;
	}
}

.footer-list {
	margin-top: 1.5rem;
	list-style: none;
	padding: 0;
	font-family: 'Kessel_105 W05 Book', serif;
	> li {
		font-size: 12px;
		margin-bottom: 0.5rem;

		a {
			color: $copy-color;
			text-decoration: none;

			&:hover,
			&:focus {
				color: $secondary-color;
			}
		}
	}
}

.footer-element-divider {
	@extend .vertical-divider;
	right: 0;
	top: 34px;
	height: 128px; // leaving this in for browsers that don't support calc
	height: calc(100% - 34px);
}

.phone-footer {
	padding-top: 5px;
	font-family: 'Kessel_105 W05 Book', serif;
	p {
		clear: both;
		font-size: 16px;
		font-weight: normal;
		white-space: normal;
	}
}

.legal-footer {
	background: $copy-color left top;
	padding: 1rem;
	p {
		white-space: normal;
	}

	&-link {
		color: $white;
		text-decoration: underline;
		&:hover {
			color: $white;
			text-decoration: underline;
		}
	}

	.equal-image {
		float: left;
		display: inline;
		margin-right: 5px;
	}

	.equal-housing-text {
		display: block;
		padding-left: 50px;
		color: $white;
		font-size: 12px;
		font-weight: bold;

		p {
			padding-top: 15px;
			padding-left: 10px;
		}

		& + p {
			padding-left: 50px;
		}
	}

	p {
		color: $white;
		font-size: 12px;
		font-weight: lighter;
		padding: .1rem 0rem;
	}
}

.legal-code, .legal-copyright {
	color: $white;
	font-size: 12px;
}

.legal-code {
	float: right;
}

.legal-copyright {
	float: left;
	padding-left: 50px;
}

@media (max-width: 767px) {
	.footer {
		&-container {
			padding-top: 0.5rem;
			padding-bottom: 0.5rem;
			.social-icons {
				margin: 0 0 1rem 0;
			}
		}
		&-content {
			margin-top: 1.5rem;
		}
		&-list {
			margin: 0;
		}
		&-element-divider {
			display: none;
		}
	}
}

@media (min-width: 768px) {
	.footer-content {
		text-align: left;
	}

	.footer-element-divider {
		display: block;
	}
}
