$text-content-padding: 2rem;
.ownership-how {
  p {
    margin: 0;
  }
  .image-description-container {
    .image-description-parent {
      .text-content {
        padding: $text-content-padding;
      }
    }
  }

  .ui-slider {
    &-range {
      background-color: $secondary-color;
    }
    &-handle {
      cursor: pointer;
      width: 26px;
      height: 27px;
      position: absolute;
      top: -55%;
      z-index: 20;
      background: none;
      background-image: url("/wp-content/plugins/mvcweb/assets/images/slider_tab.png");
      color: $ownership-slider-handle;
      box-shadow: 0 0 10px 0px rgba(197, 59, 59, 0.2);
      border: none;
      &:focus {
        outline: none;
      }
    }
    &-horizontal {
      height: 1rem;
    }
  }
  &-points {
    padding: 3rem 1rem 0 1rem;

    &-title {
      @extend .ownership-body-title-text;
      font-size: 24px;
      @include tracking-to-spacing(150);
      text-align: center;
    }

    &-slider {
      &-container {
        padding: 2rem 0 0 0;
        .ui {
          &-state {
            &-active {
              background: none !important;
              background-image: url("/wp-content/plugins/mvcweb/assets/images/slider_tab.png") !important;
              background-color: transparent !important;
              transform: scale(1.1) translate(0px, -2px);
            }
          }
          &-widget {
            &.ui-widget-content {
              border: 0;
            }
            &-content {
              height: 12px;
              border: 0;
              background-image: url("/wp-content/plugins/mvcweb/assets/images/slider_bg.png") !important;
              border-radius: 6px;
            }
          }
        }
      }

      &-label {
        font-size: 16px;
        color: $copy-color;
        font-weight: bold;
        margin-bottom: 2rem;
      }
    }
  }
  .ownership-body-element {
    padding-bottom: 1rem;
  }

  .point-slider-level {
    position: absolute;
    transform: translate(-50%, 0%);
    span {
      font-weight: bold;
      color: $secondary-color;
    }
  }

  .ui-slider-range {
    background-image: none !important;
  }
  .ui-slider-range-min {
    background-color: $secondary-color;
  }

  .point-slider-level {
    position: absolute;
    z-index: 10;
  }
  .point-slider-grey-dash {
    background-image: url("/wp-content/plugins/mvcweb/assets/images/slider_pip_nofill.png");
    border: none;
    width: 2px;
    height: 12px;
    margin: -12px auto 5px auto;
  }

  .point-slider-teal-dash {
    background-image: url("/wp-content/plugins/mvcweb/assets/images/slider_pip.png");
    border: none;
    width: 2px;
    height: 12px;
    margin: -12px auto 5px auto;
  }

  .ui-widget-header {
    background-color: transparent !important;
    background-image: url("/wp-content/plugins/mvcweb/assets/images/slider_fill_bg.png") !important;
  }
}
