.slideout {
	&-menu {
	  position: fixed;
	  top: 0;
	  bottom: 0;
	  width: 80%;
		height: 100%;
	  overflow-y: scroll;
	  -webkit-overflow-scrolling: touch;
	  z-index: 0;
	  display: none;
		background-color: $slideout-panel-bg;
		&-left {
			left: 0;
		}
		&-right {
			right: 0;
		}
	}
	&-panel {
		position: relative;
		z-index: 1;
		will-change: transform;
		background-color: $white; /* A background-color is required */
		min-height: 100vh;
		top: #{$fixedHeaderHeight - 2px};
	}
}

.panel {
	top: #{$fixedHeaderHeight};
	position: relative;
}

.mobile-menu {
	display: none;
}

.slideout-open,
.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden;
	user-drag: none;
}

.slideout-open .slideout-menu,
.slideout-open .mobile-menu {
  display: block;
}

@media screen and (min-width: $mobile-menu-break) {
	.mobile-menu {
		display: none;
	}
  .slideout-menu {
    display: none;
  }
	.slideout-open .slideout-menu,
.slideout-open .mobile-menu {
  display: none;
}
  .btn-hamburger {
    display: none;
  }
}
