.resort-booking-container {
	padding-top: 4rem;
	padding-bottom: 4rem;
	.vacation-text {
		padding-top: 0.75rem;
	}
	.map-btn {
		font-weight: bold;
	}
}

.resort-map-container {
	width: 100%;
	margin: 0;
}

.resort-header-link {
	background-color: $resort-header-link;
	font-size: 8px;
	line-height: 11.5px;
	color: $white;
	margin: 0 3px 2px 0;
	display: inline-block;
	width: 11px;
	height: 11px;
	text-align: center;
	cursor: pointer;
}

.features-item {
	&-common {
		position: relative;
		opacity: 1;
		cursor: pointer;
		margin: 5px;
		box-shadow: inset 0 -3px #C9C9C7;
		transition: all 0.3s cubic-bezier(.25,.8,.25,1);
	}
	&-hover:not(.disabled) {
		&:hover {
			top: 1px;
			box-shadow: none;
			.feature-icon {
				background-color: #E0DFDE;
			}
		}
	}
	&.disabled {
		opacity: 0.25;
		cursor: default;
	}
	&.selected {
		top: 1px;
		box-shadow: none;
		.feature-icon {
			background-color: #E0DFDE;
		}
	}
}

.is-sticky {
	.resort-menu-container {
		margin: 0;
		backface-visibility: hidden;
		z-index: 10000;
		position: fixed;
		width: 100%;
		left: 0;
	}
}

.ta-quote, .ta-quote-up {
  display: none !important;
}

.resort-header {
	&-container {
		background-color: $white;
		padding: 0;
	}
	&-back {
		text-transform: uppercase;
		color: $copy-color;
		position: relative;
		top: 1px;
		font-size: 14px;
		margin-left: 0.5rem;
		color: $secondary-color;
		text-decoration: underline;
		&:hover {
			text-decoration: none;
			color: $secondary-color;
		}
	}
}

body.hasHover {
	.features-item {
		@extend .features-item-hover;
	}
	.resort-menu-option {
		&:hover {
			text-decoration: none;
			@extend .resort-selection-active;
		}
	}
}
.feature-icon {
	position: relative;
	background-color: $activity-feature-bg;
	width: 100%;
	height: 100%;
	min-width: 150px;
	min-height: 150px;
	display: block;
	z-index: -1;
	&-content {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, -50%);
	}
	&-img {
		width: 100%;
	}
	img {
		margin: auto;
		display: block;
		transform: scale(1.5, 1.5);
	}
	&-text {
		text-align: center;
		font-size: 12px;
		position: relative;
		display: block;
		color: $copy-color;
		text-transform: uppercase;
		@include tracking-to-spacing(150);
		font-family: 'Kessel_105 W05 Book', sans-serif;
		left: 0;
		top: 1.5rem;
		margin: 0;
	}
}

.resort-menu-option-common {
	font-size: 12px;
	font-family: 'Kessel_105 W05 Book', sans-serif;
	color: $white;
	text-transform: uppercase;
	text-align: center;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
}



// Resort Menu
.resort-menu {
	&-container {
		width: 100%;
		background-color: $copy-color;
		margin-left: 0;
		margin-right: 0;
		margin-bottom: 0;
		@extend .carousel-container;
		margin-top: 7px;
		.resort-menu {
			padding-left: calc(#{$logoWidth} + 2rem);
			padding-right: calc(#{$logoWidth} + 2rem);
			flex-direction: row;
			flex-wrap: nowrap;
			&-option {
				span {
					color: $white;
				}
				&:first-child {
					border-left: 1px solid $resort-menu-divider-color;
				}
				@extend .resort-menu-option-common;
				flex-grow: 1;
				border-right: 1px solid $resort-menu-divider-color;
				@include tracking-to-spacing(100);
				font-weight: bold;
			
			}
		}
	}
}
// Internet explorer 10 hack
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
	.resort-menu-container {
		margin-top: 5px;
		.resort-menu {
			padding-left: calc(#{$logoWidth} - 1px + 2rem);
			padding-right: calc(#{$logoWidth} - 1px + 2rem);
		}
	}
}
.ta {
	&-container {
		&-quote {
			display: none;
			text-align: center;
			padding-bottom: 2rem;
			a {
				text-decoration: none;
				color: $copy-color;
			}
			.quote {
				&-content {
					& > div,
					& > img {
						margin-bottom: 0.5rem;
					}
				}
				&-meta {
					position: relative;
					top: 1px;
					font-size: 14px;
					margin-left: 1rem;
				}
				&-title {
					font-family: 'Baskerville BT WGL4 W05 Roman', serif;
				}
				&-more {
					color: $secondary-color;
					margin-left: 0.5rem;
				}
				&-text {
					padding: 0;
					white-space: normal;
				}
			}
			img {
				display: inline-block;
			}
		}
		&-review {
			a {
				text-decoration: none;
				position: relative;
				top: 0;
			}
			img {
				display: inline-block;
			}
			span {
				position: relative;
				left: 3px;
				top: 1px;
				font-family: 'Baskerville BT WGL4 W05 Roman', serif;
				color: $primary-color;
				font-size: 14px;
				margin-right: 0.2rem;
			}
		}
	}
}

.resort-mobile-touch {
	background-color: transparent;
	span {
		color: $white !important;
		&:after {
			content: '';
			position: absolute;
			bottom: -5px;
			left: -1px;
			display: block;
			height: 2px;
			background-color: $white;
			width: calc(100% + 2px);
		}
	}
}

@media only screen and (max-width: 767px) {
	.resort-menu {
		&-container {
			position: relative;
			width: 100%;
		
			.resort-menu {
				padding-left: 0;
				padding-right: 0;
				align-items: flex-start;
				overflow: auto;
				display: flex;
				&-option {
					border: none;
					flex: 1 0 auto;
					padding-left: 0.5rem;
					padding-right: 0.5rem;
					position: relative;
					span {
						color: $resort-mobile-option-inactive;
						position: relative;
						display: inline-block;
						&:hover {
							background-color: none;
							text-decoration: none;
						}
					}
					&:first-child {
						border: none;
					}
					&.resort-selection-active {
						background-color: transparent;
						span {
							color: $white;
							&:after {
								content: '';
								position: absolute;
								bottom: -5px;
								left: -1px;
								display: block;
								height: 2px;
								background-color: $white;
								width: calc(100% + 2px);
							}
						}
					}
				}
			}
		}
	}
}

@media only screen and (min-width: 768px) {
	.resort-menu {
		display: flex;
	}
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
	.resort-menu-container {
		.resort-menu {
			padding-left: 15%;
			padding-right: 15%;
		}
	}
}

@media only screen and (max-width: 991px) {
	.resort-menu {
		margin: -1px 0 0 0;
	}
}
