@import "mixins";
.logo {
  display: block;
  justify-content: center;
  left: $padding-copy-left;
  top: 0;
  position: absolute;
  top: 0;
  z-index: 200;
  width: $logoWidth;
  > img {
    width: 100%;
    height: auto;
  }
  &-mobile {
    display: inline-block;
    width: 60%;
    max-width: 300px;
    img {
      width: 100%;
    }
  }
}

.fixed-header {
  position: fixed !important;
  width: 100%;
  height: $fixedHeaderHeight !important;
  backface-visibility: hidden;
  z-index: 945; 
  max-width: 1400px;
  top: 0;
}

.mobile-menu {
  padding: 0.5rem 1rem;

  &-ad {
    display: block;
    margin: auto;
  }
  a {
    color: $white;
    text-decoration: none;
  }
  &-call {
    color: $white;
    text-align: center;
    text-transform: uppercase;
    font-size: 14px;
    margin-bottom: 1.5rem;
    margin-top: 0.5rem;
    text-decoration: underline;
    display: inline-block;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    .phone-number {
      color: $white;
    }
  }
  &-button {
    color: $white;
    text-align: center;
    border: solid $white 1px;
    border-radius: 0;
    text-transform: uppercase;
    width: 100%;
    padding: 0.5rem 0;
    margin-bottom: 1.5rem;
  }
  &-login {
    text-align: center;
    margin-bottom: 1rem;
    .login-items {
      text-transform: uppercase;
      border-bottom: 1px solid $white;
      display: inline-block;
      a {
        text-decoration: none !important;
      }
      i {
        position: relative;
        top: 1px;
        color: $white;
      }
    }
  }
  &-search {
    color: $primary-color;
    position: relative;
    width: 100%;
    margin-bottom: 0.5rem;
    input[type="text"] {
      padding: 0.5rem;
      width: 100%;
    }
    .icon-search2 {
      position: absolute;
      right: 0.5rem;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  ul,
  li {
    color: $white;
    margin: 0;
    list-style: none;
    font-family: "Kessel_105 W05 Book", sans-serif;
    @include tracking-to-spacing(100);
  }
  ul {
    &.mobile-menu-dropdown-ul {
      padding: 1rem 0;
    }
  }
  &-dropdown-li-header {
    padding: 1rem;
    font-size: 18px;
    position: relative;
    text-transform: uppercase;
    span {
      color: $white;
    }
    &:focus {
      outline: none;
    }
    i {
      position: absolute;
      top: 50%;
      right: 1rem;
      transform: translateY(-50%);
    }
  }
  li {
    &.mobile-menu-dropdown-li {
      cursor: pointer;
      border: 1px solid $secondary-color !important;
      background-color: $slideout-panel-bg;
      z-index: 1000;
      &:focus {
        outline: none;
      }
      a {
        &:hover,
        &:focus {
          outline: none;
        }
      }
      .mobile-menu-options {
        background-color: $secondary-color;
        padding: 0;
        font-size: 16px;
        box-shadow: none;
        max-height: 0px;
        transition: max-height 0.4s linear;
        &-active {
          height: auto;
          max-height: 800px;
          overflow: hidden;
          ul {
            visibility: visible;
            top: 0;
            transition-delay: 0s;
            li {
              opacity: 1;
            }
          }
        }
      }
      ul {
        position: relative;
        padding: 1rem 2rem;
        visibility: hidden;
        transition: visibility 0s linear 0.3s, top 0.3s linear;
        li {
          margin-bottom: 0.5rem;
          opacity: 0;
          transition: opacity 0.5s linear;
          &:last-child {
            margin-bottom: 0;
          }
        }
        .mobile-menu-dropdown-option {
          text-decoration: none;
          color: $white;
          text-transform: uppercase;
          margin-bottom: 0.5rem;
        }
      }
    }
  }
}

.header {
  &-info {
    @include tracking-to-spacing(50);
    position: relative;
    top: -0.4rem;
    right: 0.5rem;
    text-transform: uppercase;
    @extend .header-navbar-text;
    &-request {
      padding-top: 0.5rem;
      .marriott-btn {
        position: relative;
        top: -2px;
      }
    }
  }
  &-mobile {
    width: 100%;
    height: auto;
    position: relative;
    backface-visibility: hidden;
    background-color: $secondary-color;
    padding: 1rem;
    display: none;
    .hamburger {
      &-inner {
        background-color: $white;
        border-radius: 0;
        &:before,
        &:after {
          background-color: $white;
          border-radius: 0;
        }
      }
    }
  }
  &-toggle-button {
    display: inline;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  &-navbar {
    padding-left: 14rem;
    padding-top: 1.5rem;
    @extend .header-navbar-text;
    &-text {
      @include text(
        Kessel_105 W05 Book,
        14px,
        $copy-color,
        normal,
        normal,
        uppercase
      );
    }
    .dropdown-toggle {
      &:after {
        content: none;
      }
    }
    .icon-rounded-down {
      color: $navbar-dropdown-icon;
      font-size: 18px;
      position: relative;
      left: 0;
      top: 0.2rem;
    }
  }
}

#navbarSearchLink {
  position: relative;
  top: 1px;
}
.mobile-menu-icon {
  fill: $white;
}

.mvcweb-toggler {
  .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255,255,255, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
  }
}

.searchbar-header {
  font-size: 14px;
  position: relative;

  input {
    border-radius: 0;
    border: 1px solid $gray-element;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 8px;
    padding-right: 20px;
  }
}

.owner-login-btn {
  margin-left: 1em;
  margin-right: 2em;
  @include tracking-to-spacing(0);
}

.navbar-light .navbar-nav .nav-link {
  color: $copy-color;
}

.owner-login {
  color: $copy-color;
  font-size: 12px;
  text-decoration: none;
  text-align: right;
  font-weight: bold;
  border-bottom: 1px solid $copy-color;
  padding-bottom: -2px;
  i {
    position: relative;
    top: 1px;
  }
  &:hover {
    color: $secondary-color;
    text-decoration: none;
    border-bottom: 1px solid $secondary-color;
  }
}

.dropdown {
  &-header-menu {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  &-option {
    padding: 0.2rem 0;
    @include tracking-to-spacing(0);
    font-weight: bold;
    color: $copy-color;
  }
}

.promotion-container {
  position: relative;
  display: inline-block;
  border: 1px solid $white;
  .promotion-info {
    color: $white;
    position: absolute;
    top: 20%;
    left: 1rem;
    text-align: left;
    h3 {
      color: $white;
    font-weight: bold;
    }
    h5 {
      color: $white;
      font-weight: bold;
    }
  }
  
  .marriott-btn {
    position: absolute;
    max-width: 50%;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid $white;
  }
}

@media screen and (max-width: $mobile-menu-break) {
  .header {
    &-navbar {
      padding: 0;
    }
    &-info {
      display: none;
    }
    &-mobile {
      display: block;
    }
  }
  .logo {
    display: none;
    &-href {
      width: 60%;
      display: inline-block;
      padding-top: 0.5rem;
      padding-bottom: 0.5rem;
    }
  }

  .owner-login {
    display: none;
  }

  .navbar {
    background-color: $secondary-color;
    display: none;
  }
}

@media screen and (min-width: $mobile-menu-break) {
  .header {
    &-navbar {
      display: flex !important;
      flex-basis: auto;
      flex: auto;
      align-items: center;
      &-dropdown {
        position: relative;
        top: -2px;
        cursor: pointer;
        transition: all $dropdown-transition-time;
        i {
          transition: all $dropdown-transition-time;
        }
        &:last-child {
          top: 0;
        }
        &:after {
          background-color: $secondary-color;
          height: 2px;
          width: 80%;
          left: 0;
          bottom: 0;
          content: "";
          opacity: 0;
          position: relative;
          transition: opacity $dropdown-transition-time;
          display: block;
          z-index: 1000;
        }
        &-option {
          display: block;
          color: $copy-color;
          &:hover {
            text-decoration: none;
            font-weight: bold;
            color: $secondary-color;
          }
        }
        &:hover {
          color: $secondary-color;
          &:after {
            opacity: 1;
          }
          ul {
            opacity: 1;
            visibility: visible;
            border: 1px  solid $gray-element;
          }
        }
        ul {
          background: $white;
          font-size: 12px;
          padding: $dropdown-box-padding-top-bottom $dropdown-box-padding-left-right;
          position: absolute;
          top: 42px;
          left: $dropdown-left;
          z-index: 1000;
          -webkit-box-shadow: none;
          -moz-box-shadow: none;
          box-shadow: none;
          visibility: hidden;
          opacity: 0;
          transition: opacity $dropdown-transition-time;
          li {
            display: block;
            margin-bottom: 3px;
            margin-top: 3px;
            white-space: nowrap;
            &:first-child {
              margin-top: 5px;
            }
            &:last-child {
              margin-bottom: 0;
            }
            &:hover {
              background: $white;
            }
          }
        }
      }
    }
  }
  .navbar {
    padding: 0;
    display: flex;
    background-color: $white;
    &:after {
      content: '';
      background-color: $gray-element;
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 1px;
    }
    &-dark {
      .navbar-nav {
        @include tracking-to-spacing(100);

        .show > .nav-link {
          color: $primary-color;
        }

        .nav-link {
          color: $primary-color;
          white-space: nowrap;
          display: inline-block;
          display: inline-block;
            &:hover,
          &:focus {
            color: $secondary-color;
            font-weight: bold;
            i {
              color: $secondary-color;
            }
          }
          &:before {
            display: block;
            content: attr(title);
            font-weight: bold;
            height: 0;
            overflow: hidden;
            visibility: hidden;
          }
          span {
            display: inline-block;
            &:hover,
          &:focus {
            font-weight: bold;
            i {
              color: $secondary-color;
            }
          }
          &:before {
            display: block;
            content: attr(title);
            font-weight: bold;
            height: 0;
            overflow: hidden;
            visibility: hidden;
          }
          }
          
        }
      }
    }
  }
  .logo {
    width: $logoWidth;
  }

  .owner-login {
    position: absolute;
    top: 0.2rem;
    right: 1rem;
  }

  .menu {
    padding-left: 12.5rem;
  }
}

@media (min-width: 1130px) {
  .logo {
    width: $logoWidth;
  }
}
