.inspiration {
	.vacation-greatness {
		padding: 3rem 0 !important;
	}
	.home-banner {
		padding-top: 0;
	}
	.share {
		padding: 0;
		position: relative;
		width: 100%;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center center;
		&-content {
			padding-top: 7rem;
			padding-bottom: 5rem;
			.title {
				font-size: 55px;
				color: $white;
			}
			.social-icons {
				i {
					color: $white;
					font-size: 30px;
					margin-left: 10px;
					margin-right: 10px;
					&:hover {
						color: $secondary-color;
					}
				}
			}
		}
	}
	.header-underline {
		position: relative;
		display: inline-block;
		&:after {
			height: 7px;
			background-color: $activities-accent;
			left: calc(50% - 15px);
			top: 0px;
		}
	}
	.home-banner.header-underline {
		&:after {
			left: calc(50% - 35px);
		}
	}
	.inspiration-item-collection {
		position: relative;
		a {
			width: 100%;
			height: 100%;
			text-decoration: none;
		}
		.inspiration-item-page {
			.item {
				@extend .cover-picture-container;
				z-index: 0;
				color: $white;
				h3, h4 {
					color: $white;
				}
				img {
				  @extend .cover-picture
				}
				&-content-container {
					position: relative;
					width: 100%;
					height: 100%;
					&:before {
						content: "";
						display: block;
						padding-top: 75%;
						background-color: rgba(0,0,0,0.3);
					}
					.content {
						position: absolute;
						top: 25%;
						left: 0;
						bottom: 0;
						right: 0;
						.title {
							font-size: 18px;
							text-transform: uppercase;
							@include tracking-to-spacing(150);
						}
						.subtitle {
							padding: 2rem 4rem 0 4rem;
							text-align: center;
							font-size: 42px;
							font-family: 'Baskerville BT WGL4 W05 Roman', serif;
							@include tracking-to-spacing(-20);
							@include leading-to-line-height(55, 42);
						}
						.header-underline {
							&:after {
								background-color: $white;
								width: 30px;
								height: 2px;
								top: 2px;
							}
						}
						.btn {
							text-align: center;
							border: solid $white 1px;
							padding: 10px 10%;
							font-weight: normal;
							border-radius: 0;
							margin-top: 2rem;
							text-transform: uppercase;
							transition: initial;
							color: $white;
	
							&:focus {
								box-shadow: 0;
							}
						}
					}
				}
			}
		}
	}


	.pagination {
		width: 90%;
		padding: 1rem;
		justify-content: center;
		position: absolute;
		top: 0;
		left: 5%;
		background-color:$white;
		border-radius: 0;
		z-index: 1000;
		margin: 0; 
	}
}

.marriott-page-item {
	background-color: $primary-color;
	color: $white;
	border-radius: 0;
	padding: 0.5rem 1rem;
	cursor: pointer;
	a {
		color: $white;
		&:hover {
			text-decoration: none;
		}
	}
	&.active, &:hover {
		background-color: $secondary-color;
	}
	margin-right: 1rem;
	&:last-child {
		margin-right: 0;
	}
}

@media only screen and (max-width: 430px) {
	.inspiration .inspiration-item-collection .inspiration-item-page .item-content-container .content {
		top: 15%;
	}
	.inspiration .inspiration-item-collection .inspiration-item-page .item-content-container .content .title {
		font-size: 12px;
	}
	.inspiration .inspiration-item-collection .inspiration-item-page .item-content-container .content {
		.subtitle {
			font-size: 18px;
			padding: 1rem;
		}
		.btn {
			margin-top: 0;
			padding: 5px 5%;
			font-size: 12px;
		}
	}
}

@media only screen and (min-width: 431px) and (max-width: 575px) {

	.inspiration {
		.pagination {
			width: 80%;
			padding: 0.75rem;
			left: 10%;
			.marriott-page-item {
				padding: 0.25rem 0.75rem;
			}
		}
		.share {
			&-content {
				padding-top: 5rem;
				padding-bottom: 3rem;
				.title {
					font-size: 40px;
				}
				.social-icons i {
					font-size: 25px;
				}
			}	
		}
	}
	.inspiration .inspiration-item-collection .inspiration-item-page .item-content-container .content {
		.subtitle {
			font-size: 28px;
			padding: 2rem 2rem;
		}
		.btn {
			margin-top: 0;
			padding: 5px 5%;
			font-size: 14px;
		}
	}
}

@media only screen and (min-width: 576px) and (max-width: 767px) {
	.inspiration .share {
			&-content {
			.title {
				font-size: 40px;
			}
		}
	}
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
	.inspiration .inspiration-item-collection .inspiration-item-page .item-content-container .content .subtitle {
		font-size: 18px;
	}


	.inspiration .inspiration-item-collection .inspiration-item-page .item-content-container .content .title {
		font-size: 12px;
	}

	.inspiration .inspiration-item-collection .inspiration-item-page .item-content-container .content .btn, .inspiration .inspiration-item-collection .inspiration-item-page .item-content-container .content .marriott-btn {
		font-size: 0.75rem;
		margin-top: 1rem;
		padding: 7px 8%;
	}

	.inspiration .share {
			&-content {
			.title {
				font-size: 40px;
			}
		}
	}

}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
	.inspiration {
		.home-banner {
			padding-top: 4rem;
		}
		.share {
			&-content {
				.title {
					font-size: 50px;
				}
			}
		}
	}
	.inspiration .inspiration-item-collection .inspiration-item-page .item-content-container .content .subtitle {
		font-size: 28px;
	}
}
