.general-info {
  padding: 6rem;
  p {
    white-space: normal;
  }
  .resort-header-link {
    position: relative;
    top: -2px;
  }

  &.terms-of-use {
    .general-info-cursive {
      margin-top: 2rem;
    }
  }
  &-title-underline {
    @extend .header-underline;
    background-color: $secondary-color;
    height: 6px;
    width: 27px;
    left: 0;
  }
  &-privacy {
    padding: 6rem 6rem 3rem 6rem;
    p {
      white-space: normal;
    }
    a {
      @extend .general-info-link;
      text-decoration: underline;
    }
    &-accordion {
      padding: 0;
    }

    .ui-accordion {
      font-family: Arial;
      font-size: 16px;
      &-header {
        color: $copy-color;
        background-color: $accordion-background;
        background-image: none;
        border-radius: 0;
        border: none;
        font-family: Arial;
        font-weight: bold;
        &-icon {
          &.ui-icon {
            color: black;
            position: absolute;
            right: 15px;
            top: 50%;
          }
        }
      }
      &-content {
        overflow: hidden;
        color: $copy-color;
      }
      .ui-state-active {
        color: $copy-color;
        background-color: $accordion-background;
        border: none;
        background-image: none;
      }
    }
  }
  &-cookies {
    @extend .general-info-privacy;
    padding-bottom: 0;
    &-bottom {
      @extend .general-info-privacy;
      padding-top: 3rem;
    }
    table {
      width: 100%;
    }
  }
  &-title {
    font-size: 36px;
    padding-bottom: 2rem;
    &:after {
      @extend .general-info-title-underline;
    }
  }
  &-header {
    font-size: 18px;
    text-transform: uppercase;
    margin-bottom: 1rem;
    color: $secondary-color;
  }

  &-cursive {
    font-size: 18px;
    font-style: italic;
  }
  &-link {
    color: $secondary-color;
    &:hover {
      color: $secondary-color;
    }
  }
  .privacy-accordion {
    a {
      color: $copy-color;
      text-decoration: none;
      &.general-info-link {
        color: $secondary-color;
      }
    }
  }
}

@media only screen and (max-width: 767px) {
  .general-info {
    padding: 3rem 1.5rem;
    &-privacy {
      padding: 3rem;
    }
  }
}
