
.carousel-container {
	padding: 0;
	width: 100%;
	height: auto;
	max-width: $body-max-width;
	margin: 0 auto;
	&:hover {
		.carousel-prev, .carousel-next {
			opacity: 1;
		}
	}
}

.carousel-prev,
.carousel-next {
	opacity: 0;
	transition: opacity 0.2s ease-in-out;
	position: absolute;
	z-index: 100;
	top: 50%;
	transform: translateY(-50%);
	background-color: $secondary-color;
	-webkit-appearance: none;
	-moz-appearance: none;
	cursor: pointer;
	border: none;
	width: 64px;
	height: 80px;
	&:hover, &:focus {
		outline: none;
	}
}

.center-carousel-extras {
	left: $carouselCenterPaddingLeft;
	max-width: $body-max-width;
	width: calc(100% - ((#{$logoWidth} + 2rem) * 2)) !important;
}

.carousel-content {
	position: relative;
}

.slide-overlay {
	&.slick-active {
		&:before {
			opacity: 0;
		}
	}
	&:before {
		@include overlay($carousel-blend);
	}
}

.ta-img {
	&-container {
		display: none;
		position: absolute;
		top: 0.5rem;
		right: 0.5rem;
		width: 30%;
		justify-content: flex-end;
		flex-flow: row nowrap;
	}
	&-item {
		flex: 1 1 100%;
		min-width: 0;
		max-width: 50% !important;
		max-height: 50% !important;
		align-self: flex-start;
	}
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active){
	/* IE10+ CSS */
	.ta-img {
		&-container {
			width: 20vw;
		}
	}
}

.home-carousel {
	&-container {
		@extend .carousel-container;
		.slider-caption {
			text-align: left;
			max-width: $body-max-width;
			transform: translateY(-50%);
			padding: 0;
			position: absolute;
			left: 11%;
			top: 40%;
			width: 100%;
		
			.slider-caption-text {
				font-size: $slider-caption-text-size;
				color: $white;
				font-weight: bold;
				line-height: 1.2;
			}
		
			.btn {
				background: none;
				text-align: center;
				border: solid $white 2px;
				color: $white;
				padding: 15px;
				border-radius: 0;
				margin-top: 2rem;
				text-transform: uppercase;
				transition: initial;
				-webkit-appearance: none;
				-moz-appearance: none;
				@include tracking-to-spacing(50);
				&:focus {
					box-shadow: unset;
				}
			}
		
			a {
				color: $white;
				&:hover,
				&:focus {
					color: $white;
				}
		
				&:visited {
					color: $white;
				}
			}
		}

		.slick-next {
			right: 0;
		}

		.slick-prev {
			left: 0;
		}
		.slick-slide {
			display: flex;
			position: relative;
			&:focus {
				outline: none;
			}
			img {
				width: 100%;
				height: 100%;
				margin: auto;
				position: static;
				&:focus {
					outline: none;
				}
				
			}
		}
		.icon-rounded-right,
		.icon-rounded-left {
			font-size: $slider-btn-icon-size;
			z-index: 10;
			&:before {
				color: $white;
			}
		}
		

		.carousel-prev {
			left: 0;
		}

		.carousel-next {
			right: 0;
		}
	}
	&-dots {
		bottom: 1rem;
		position: absolute;
		display: block;
		width: 100%;
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
	
		li {
			margin: 0 5px;
			position: relative;
			display: inline-block;
			width: 12px;
			height: 12px;
			padding: 0;
			cursor: pointer;
			border-radius: 50%;
			border: solid $gray-element;
			border-width: 1px;
			&.dot-active {
				background-color: $white;
			}
		}
	}
}

.carousel-center {
	&-dots {
		@extend .center-container;
		@extend .center-carousel-extras;
		position: absolute;
		.dots-info {
			display: inline-block;
			padding: 0.25rem 1.5rem;
			background-color: rgba(0, 0, 0, 0.5);
			position: absolute;
			left: 50%;
			bottom: 5%;
			transform: translate(-50%, -50%);
			color: $white;
			font-size: 18px;
		}
	}

	img {
		width: 100%;
		height: 100%;
	}
	@extend .carousel-container;

	.slick-slide {
		@extend .carousel-content;
		max-height: 654px;
		position: relative;
		
	}

	.slick-next {
		right: 0;
	}

	.slick-prev {
		left: 0;
	}
}

.gallery-caption {
	position: absolute;
	top: 5%;
	right: 0;
	background-color: rgba(0, 0, 0, 0.5);
	color: $white;
	text-align: center;
	padding: .5rem 1rem;
	display: none;
	&:before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		background-color: $secondary-color;
		width: 5px;
		display: block;
	}
}

.new-arrows {
		position: absolute;
		top: 50%;
		width: 100%;
		max-width: 1400px;
		.icon-rounded-right,
		.icon-rounded-left {
			font-size: 30px;;
			&:before {
				color: $white;
			}
		}

		.carousel-prev,
		.carousel-next {
			position: absolute;
			background-color: $secondary-color;
			-webkit-appearance: none;
			-moz-appearance: none;
			cursor: pointer;
			border: none;
			width: 64px;
			height: 80px;
			&:hover, &:focus {
				outline: none;
			}
		}

		.carousel-prev {
			left: 0;
		}

		.carousel-next {
			right: 0;
		}

}

.new-arrows-center {
	@extend .new-arrows;
	@extend .center-container;
	@extend .center-carousel-extras;
	position: absolute;
	.carousel-prev,
	.carousel-next {
		transform: translateY(-50%);
	}
}

@media (max-width: 767px) {
	.carousel-container {
		&:hover {
			.carousel-prev, .carousel-next {
				display: none;
			}
		}
	}
	.slide-overlay {
		&:before {
			display: none;
		}
	}
	.slick-slide {
		.gallery-caption {
			font-size: 2.5vw;
		}
	}
	.carousel-center-dots {
		width: 100% !important;
		left: 0;
		.dots-info {
			font-size: 2.5vw;
		}
	}
	.new-arrows-center {
		left: 0;
		.icon-rounded-right,
		.icon-rounded-left {
			font-size: 30px;
			font-size: 5vw;
			&:before {
				color: $white;
			}
		}
		.carousel-prev, .carousel-next {
			width: 10%;
			max-width: 64px;
			height: auto;
			max-height: 80px;
			padding: 0.5rem 0;
		}
	}
	.home-carousel-container {
		.slider-caption {
			width: auto;
			.slider-caption-text {
				font-size: 35px;
			}
			.btn {
				white-space: normal;
				font-size: 10px;
				padding: 8px;
				margin-top: 0.5rem;
				@include tracking-to-spacing(50);
			}
		}
		.carousel-prev,
		.carousel-next {
			width: $slider-btn-next-width * 0.5;
			height: $slider-btn-next-height * 0.5;
		}
		.icon-rounded-right,
		.icon-rounded-left {
			font-size: $slider-btn-icon-size * 0.5;
		}
	}
}



@media (min-width: 768px) {
	.new-arrows-center {
		.icon-rounded-right,
		.icon-rounded-left {
			font-size: 30px;
			&:before {
				color: $white;
			}
		}

		.carousel-prev,
		.carousel-next {
			position: absolute;
			background-color: $secondary-color;
			-webkit-appearance: none;
			-moz-appearance: none;
			cursor: pointer;
			border: none;
			width: 64px;
			height: 80px;
			&:hover, &:focus {
				outline: none;
			}
		}

		.carousel-prev {
			left: 0;
			transform: translateY(-50%);
		}

		.carousel-next {
			right: 0;
			transform: translateY(-50%);
		}
	}

	.slick-slide {
		.gallery-caption {
			min-width: 15%;
			font-size: 16px;
		}
	}

	.home-carousel-container {
		.slider-caption {
			.slider-caption-text {
				font-size: $slider-caption-text-size * 0.7;
			}
		}
		.carousel-prev,
		.carousel-next {
			width: $slider-btn-next-width * 0.8;
			height: $slider-btn-next-height * 0.8;
		}
		.icon-rounded-right,
		.icon-rounded-left {
			font-size: $slider-btn-icon-size * 0.8;
		}
	}
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
	.center-carousel-extras {
		left: 15%;
		width: 70% !important;
	}
}

@media (min-width: 992px) {
	.slider-caption {
		.slider-caption-text {
			font-size: $slider-caption-text-size;
		}
	}
	.home-carousel-container {
		.carousel-prev,
		.carousel-next {
			width: $slider-btn-next-width;
			height: $slider-btn-next-height;
		}
		.icon-rounded-right,
		.icon-rounded-left {
			font-size: $slider-btn-icon-size;
		}
	}
}
