.ownership {
	.break {
		&:after {
			@extend .header-underline;
			background-color: $secondary-color;
			height: 7px;
			width: 44px;
			top: -2px;
			left: $padding-copy-left;
		}
	}
	&-lifestyle-container {
		P {
			padding-top: 0.5rem;
		}
	}
	&-col {
		[class*='col-'] {
			padding: 0;
		}
	}
	&-body {
		&-title {
			&-text {
				@extend .section-header;
				padding-left: 1rem;
			}
			&-text ~ p {
				padding-left: 1rem;
			}
		}
		&-element {
			padding: 1rem 1rem;
		}
	}
	&-text {
		font-size: 16px;
		white-space: pre-line;
		margin: 0;
	}
}

.resort-preview {
	&-container {
		padding-top: 2rem;
		[class*='col-'] {
			margin-bottom: 2rem;
		}
	}
	&-experience {
		margin-bottom: 2rem;
	}
	&-parent {
		border-left: 1px solid $vertical-divider-color;
		border-right: 1px solid $vertical-divider-color;
		border-bottom: 1px solid $vertical-divider-color;
		height: 100%;
		img {
			width: 100%;
		}
		.read-more-btn {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 50%);
		}
		.info-accent {
			background-color: $secondary-color;
			margin-top: -1rem;
			width: 2.5rem;
			height: 1rem;
		}
		.info-container {
			padding: 1rem 2rem 2rem 1rem;
			.title {
				font-size: 24px;
				margin-top: 1rem;
				font-family: "Baskerville BT WGL4 W05 Roman", serif;
				text-transform: none;
				color: $copy-color;
				letter-spacing: 0;
			}
			.location {
				font-size: 16px;
				color: $secondary-color;
				text-transform: uppercase;
				@include tracking-to-spacing(150);
			}
			.summary, .type {
				display: none;
			}
			.resort-elements {
				list-style: none;
				@include colored-bullet($secondary-color);
			}
			.text {
				font-size: 16px;
				color: $copy-color;
				margin-bottom: 1rem;
			}
		}
	}
}
