/***offers-a and offers-b Mobile Version***/

#wrapper_offers_b.page-parent,
#wrapper_offers_a.page-parent{
    .header-desktop {
        background-image: none;
        border-bottom: 1px solid #fff;
        height: 101px;
        margin: 0 5% 0 5%;
    }
}
#wrapper_offers_a,
#wrapper_offers_b{
    .offer-image {
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 100%;
        min-height: auto;
        height: 400px;
    }
}
#wrapper_offers_b,
#wrapper_offers_a{
    .header-desktop {
        display: block;
    }
    .price-now{
        padding: 0px 15px;
        font-size: 1.1rem;
        font-weight: 600;
        color: #009587;
        font-family: Arial,sans-serif;
    }
    #page-content-wrapper {
        overflow-x: hidden;
    }
    #taQuoteContainer{
        position: relative;
    }
    #quoteCol {
        border: 1px solid #009587;
        .resort-action{
            #taReviewImg,
            #taReviewNumber{
                display: none;
            }
        }
    }
    .teal-border {
        border: none;
    }
    .ta-quote, .ta-quote-up {
        display: block!important;
        padding: 7px 0px;
        border-radius: 50%;
        img {
            width: 23px;
        }
    }
    .ta-quote {
        left: -12px;
        top: 11px;
    }
    .ta-quote-up  {
        right: -12px;
        bottom: 11px;
    }
    #taReviewNumber {
        color: #009587;
        text-decoration: underline;
        font-family: Arial,sans-serif;
        font-size: 11px;
        padding: 0 10px 0 5px;
    }
    .quote-title {
        font-style: italic;
        font-weight: 600;
    }
    .quote-text {
        padding: 0 20px;
        text-align: left;
        font-family: "Kessel_105 W05 Book";
    }
    .ta-container-quote {
        padding-bottom: 0rem;
        .quote-more{
            text-decoration: underline;
            font-weight: 600;
        }
        .quote-meta {
            font-size: 12px;
            font-weight: 600;
            font-family: "Kessel_105 W05 Book";
            margin: 0px;
        }
    }
    input.form-submit {
        width: 100%;
    }
    
    .teal-fg.tap-details {
        font-size: 13px;
        text-decoration: underline;
    }
    .goldline{
        margin-top: 2rem!important;
    }
    .headerrectangle {
        max-width: 100%;
        padding: 40px 44px 18px!important;
        right: 16%;
        margin-bottom: 8px;
    }
    .whiteline {
        width: 80px;
        box-shadow: #212121 0px 2px 4px;
    }
    .whiteline.whiteline-from{
        width: 133px;
    }
    .headerrectangle h1 {
        font-size: 32pt !important;
        line-height: 0.9;
        margin-bottom: 0px;
        margin-top: 24px;
    }

    .rectanglecopy {
        text-shadow: #212121 0px 2px 4px;
    }
    .rectanglemoney > h2 {
        font-size: 35pt;
        letter-spacing: 3px;
        margin-top: 10px;
        margin-bottom: 0px;
        text-shadow: #212121 0px 2px 4px;
    }
    .percentoff {
        font-family: Kessel_105 W05 Bold;
        letter-spacing: 2px;
    }
    .from {
        font-size: 25px;
        text-transform: capitalize;
    }

    .perstay-money {
        font-family: Kessel_105 W05 Bold;
        color: #fff;
        font-size: 20pt;
        letter-spacing: 3px;
        text-transform: uppercase;
        line-height: 0.8;
    }

    .retailvalue-money {
        font-family: Kessel_105 W05 Thin;
        color: #fff;
        font-size: 10pt;
        text-transform: uppercase;
        letter-spacing: 1px;
        margin: 9px 0 20px;
    }
    .timeshare {
        font-family: "Kessel_105 W05 Bold", sans-serif;
        font-size: .95rem;
    }
    
    .days>h4 {
        font-size: 1.3em;
    }
    .money > h4 {
        font-size: 12pt;
        span {
            font-size: 17.5pt;
            color: #009587;
            letter-spacing: -1px;
            line-height: 1;
        }
    }
    .image-description-border {
        padding-bottom: 40px !important;
        .for-details {
            color: #009587;
            font-size: 10.3pt;
            letter-spacing: 1px;
            margin-top: 24px !important;
        }
    }
    
    .infodetails {
        display: flex;
        flex-direction: row;
        margin-left: 100px;
        margin-top: 35px;
        margin-bottom: 35px;
    }
    .infodetails {
        position: relative;
    } 
    .infodetails > img {
        position: absolute;
        bottom: -61px;
        right: 0;
        transform: rotate(-90deg);
        width: 300px;
    } 
    .companyinfo {
        margin-right: 53px !important;
    }
    .companyinfo h6 p,
    .legalinfo h6 p{
        margin-bottom: 0;
    }
    .phone .teal-fg {
        font-size: 32px;
        font-weight: 500;
    }
    
    @media (min-width: 768px) {
        .form-check{
            width: 100%;
            padding: 0px 0 10px;
            .form-check-label {
                white-space: nowrap;
            }
            .form-check-input {
                margin: 0;
                position: relative;
                top: 2px;
            }
            .check-yes{
                margin-right: 7px;
            }
            .optindetails {
                display: inline;
            }
        }
        
        .mobile-block{
            display: none!important;
        }
        .header-mobile {
            display: none;
        }
    }
    .tablet-hidden {
        display: block !important;
    }
    .tablet-block {
        display: none !important;
    }
}

.price-perstay {
    color: #009587;
    text-transform: uppercase;
    font-size: 11px;
    font-family: Arial,sans-serif;
    font-weight: 600;
    line-height: 1;
    padding-left: 5px;
}

@media (max-width: 767px) {
    #wrapper_offers_a, 
    #wrapper_offers_b{
      .infodetails {
        margin-left: 5px;
        padding-left: 1rem;
        display: block;
      }  

      .companyinfo {
        margin-bottom: 2rem;
      }
        .header-desktop {
            display: none;
        }
    }
}

@media (min-width: 768px) {
    .price-perstay {
        font-size: 20pt;

        br {
            content: " ";
            display: none;
        }
    }
}
@media (max-width: 991px) {
    #wrapper_offers_b,
    #wrapper_offers_a{
        .wrapp-column-reverse{
            flex-direction: column-reverse !important;
        }
        .choice > .desktop-block {
            display: none!important;
        }
        .call-us-at.mobile-block {
            display: block!important;
        }
        .tablet-hidden {
            display: none !important;
        }
        .tablet-block {
            display: block !important;
        }
    }
    #wrapper_offers_b {
        .cc-quote {
            margin-top: 40px;
        }
    }
    #wrapper_offers_a{
        .call-us-at{
            .goldline2 {
                margin-top: 20px;
            } 
        }
    }
}

