.newsroom {
	&-back {
		color: $secondary-color;
		text-transform: uppercase;
		text-decoration: underline;
		font-size: 10pt;
		margin-bottom: 1rem;
		display: inline-block;
	}
	&-title {
		padding: 7rem 12rem 0 12rem;
		font-size: 40px;
		
		&:after {
			@extend .general-info-title-underline;
			top: 0;
		}
	}
	&-media-link {
		@extend .general-info-link;
		text-decoration: underline;
	}
	&-article {
		padding-top: 1rem;
		&-container {
			padding-left: 0;
			padding-right: 3rem;
		}
		&-detail {
			&-title {
				font-size: 30px;
			}

			&-date {
				color: $secondary-color;
				font-size: 18px;
				margin-bottom: 2rem;
			}
		}
		&-title {
			font-size: 20px;
			color: $secondary-color;
			padding-bottom: 0.5rem;
			a {
				color: inherit;
			}
		}
		&-date {
			font-weight: bold;
			font-size: 16px;
			color: $copy-color;
			text-decoration: none;
			&:hover {
				text-decoration: none;
				color: $copy-color;
			}
		}
		&-description {
			padding-top: 1rem;
		}
		&-link {
			@extend .newsroom-media-link;
			font-size: 12px;
		}
		&-pdf-download {
			padding-top: 5px;
			padding-bottom: 1.5rem;
			img {
				display: block;
			}
			a {
				display: block;
			}
		}
	}
	&-content {
		padding: 0 4rem 1rem 12rem;
		p {
			white-space: normal;
		}
		a {
			@extend .general-info-link;
		}
	}
	&-contacts {
		padding-left: 0;
		&-item {
			font-size: 12px;
			.general-info-header {
				padding-top: 1rem;
			}
		}
	}

}

@media (max-width: 1130px) {
	.newsroom {
		&-title {
			padding: 1rem 2rem;
			margin: 0;
		}
		&-article {
			padding-top: 1rem;
			&-container {
				padding-left: 0;
			}
		}
		&-content {
			padding: 1rem 2rem;
			p {
				white-space: normal;
			}
		}
	}
}
